<template>
<div :class="{'navigator-wrapper': true, 'active': isActive}">
  <div class="navigator-inner">
    <md-content class="md-accent navigator-button" v-on:click.stop="toggleNavigator()">
      <md-icon v-if="!isActive">keyboard_arrow_left</md-icon>
      <md-icon v-else>keyboard_arrow_right</md-icon>
    </md-content>
    <div class="navigator-form">
      <md-toolbar md-elevation="0">
        <span>本人</span>
      </md-toolbar>
      <md-list>
        <md-list-item to="/my">
          <md-icon>radio_button_unchecked</md-icon>
          <span class="md-list-item-text">アカウント管理</span>
        </md-list-item>
      </md-list>
      <md-list>
        <md-list-item to="/my/sell">
          <md-icon>radio_button_unchecked</md-icon>
          <span class="md-list-item-text">売出物件公開</span>
        </md-list-item>
      </md-list>
      <md-list>
        <md-list-item to="/my/buy">
          <md-icon>radio_button_unchecked</md-icon>
          <span class="md-list-item-text">買取要望公開</span>
        </md-list-item>
      </md-list>
      <md-list>
        <md-list-item to="/my/rent">
          <md-icon>radio_button_unchecked</md-icon>
          <span class="md-list-item-text">貸付物件公開</span>
        </md-list-item>
      </md-list>
      <md-list>
        <md-list-item to="/my/borrow">
          <md-icon>radio_button_unchecked</md-icon>
          <span class="md-list-item-text">借入要望公開</span>
        </md-list-item>
      </md-list>
    </div>
  </div>
</div>
</template>

<script>
export default {
  pros: ['manager'],
  data: () => ({
    isActive: false
  }),
  methods: {
    toggleNavigator () {
      this.isActive = !this.isActive
    }
  }
}
</script>

<style scoped lang="scss">
.navigator-wrapper {
  position: fixed;
  right: 0;
  top: 90px;
  width: 230px;
  z-index: 20;
  transform: translateX(230px);
  transition: all .2s;
}
.navigator-wrapper.active {
  transform: translateX(0);
}
.navigator-inner {
  position: relative;
}
.navigator-button {
  position: absolute;
  top: 0;
  left: -65px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  width: 65px;
  height: 65px;
  border-radius: 10px 0 0 10px;
  transition: 0.1s;
  cursor: pointer;
  opacity: 0.4;
  overflow: hidden;
  .md-icon {
    font-size: 36px !important;
    color: #fff;
  }
}
.active .navigator-button,
.navigator-button:hover {
  opacity: 1;
}
.navigator-form {
  border-top: 10px solid #e8e8e8;
  border-left: 10px solid #e8e8e8;
  border-bottom: 10px solid #e8e8e8;
}
</style>



