<template>
<div class="md-layout md-gutter">
  <div class="md-layout-item md-size-100">
    <md-toolbar md-theme="light" class="md-dense md-primary">
      <h3 class="md-title">
        買取要望 ({{manager.estateBuyRequestService.buyRequests.length}} / {{manager.estateBuyRequestService.count}})
      </h3>
    </md-toolbar>
  </div>
  <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in manager.estateBuyRequestService.buyRequests" :key="request._id">
    <buyRequestCard :manager="manager" :object="request"/>
  </div>
  <div class="md-layout-item md-size-100 more-container" v-if="manager.estateBuyRequestService.buyRequests.length < manager.estateBuyRequestService.count">
    <md-button class="md-primary more-button" @click="more()">結果をもっと表示 ({{manager.estateBuyRequestService.buyRequests.length}} / {{manager.estateBuyRequestService.count}})</md-button>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import buyRequestCard from '@/components/shared/buyRequestCard'
export default {
  props: ['manager'],
  components: {
    buyRequestCard
  },
  methods: {
    more () {
      this.manager.estateBuyRequestService.moreSell()
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
}
.md-layout-item {
  margin: 20px 0;
}
.more-container {
  display: flex;
  justify-content: center;
}
.more-button {
  width: 300px;
}
</style>



