import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import RentPiece from '@/store/models/rentPiece.js'

class MyRentPieceService {
  constructor () {
    this._rentPieces = []
    this._editRentPiece = null
    this._count = null
    this.pagesize = 10
    this.pageindex = 1
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get rentPieces () {
    return this._rentPieces
  }
  get editRentPiece () {
    return this._editRentPiece
  }
  set editRentPiece (piece) {
    this._editRentPiece = new RentPiece(piece)
  }

  init () {
    this._rentPieces.splice(0, this._rentPieces.length)
    this._count = 0
    this.pageindex = 1
  }

  preloadMyRent = (to, from, next) => {
    this.init()
    this.findRentPiece({contact: manager.user._id}, 1).then(
      () => {
        next()
      }
    )
  }

  async findRentPiece (params, page) {
    let that = this
    if (page && page >= 1) page--
    await utils.restGet('/private/findRentPieces', {filter: params, page: page}).then(response => {
      let datas = response.datas
      that.count = response.count
      that._rentPieces.splice(0, that._rentPieces.length)
      if (datas) {
        datas.forEach(one => {
          that._rentPieces.push(new RentPiece(one))
        })
      }
    })
  }

  preloadMyRentPiece = (to, from, next) => {
    let that = this
    if (to.params.id === 'new') {
      that.editRentPiece = new RentPiece({})
      next()
    } else {
      utils.restGet('/private/findRentPieces', {'_id': to.params.id}).then(
        response => {
          if (response) {
            that.editRentPiece = new RentPiece(response.datas[0])
            next()
          }
        }
      )
    }
  }

  insertRentPiece (piece, next) {
    utils.restPut('/private/insertRentPiece', piece).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  async publishRentPiece (piece) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/publishRentPiece', {_id: piece._id, isPublishing: true}).then(savedPiece => {
      if (!savedPiece) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findRentPiece({}, 1)
  }

  async unPublishRentPiece (piece) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/unPublishRentPiece', {_id: piece._id, isPublishing: false}).then(savedPiece => {
      if (!savedPiece) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findRentPiece({}, 1)
  }

  updateRentPiece (piece, next) {
    utils.restPost('/private/updateRentPiece', piece).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  deleteRentPiece (piece, next) {
    let that = this
    utils.restPost('/private/deleteRentPiece', {_id: piece._id}).then(savedPiece => {
      if (savedPiece) {
        that.init()
        that.findRentPiece({}, 1)
        next()
      }
    })
  }
}

export default new MyRentPieceService()



