import utils from '@/tool/utils.js'
import SellPiece from '@/store/models/sellPiece.js'

class EstateSellPieceService {
  constructor () {
    this._sellPieces = []
    this._count = 0
    this.page = 0
    this.sellPieceDetail = null

    this.searchCondition = {
      stations: [],
      minute: '',
      layouts: '',
      area: '',
      type: '',
      structure: '',
      age: '',
      min: '',
      max: '',
      releday: ''
    }
  }
  get sellPieces () {
    return this._sellPieces
  }
  set sellPieces (pieces) {
    if (pieces) {
      pieces.forEach(one => {
        // 重複を削除
        this._sellPieces.filter(sellPiece => {
          return sellPiece._id === one._id
        })
        // リストに追加
        this._sellPieces.push(new SellPiece(one))
      })
    }
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }

  init () {
    this._sellPieces.splice(0, this._sellPieces.length)
    this.count = 0
    this.page = 0
  }

  preloadSellPiece = (to, from, next) => {
    let that = this

    utils.restGet('/public/findSellPieces', {}).then(
      response => {
        if (response) {
          that.init()
          that.count = response.count
          that.sellPieces = response.datas
          next()
        }
      }
    )
  }

  moreSellPiece () {
    let that = this
    utils.restGet('/public/findSellPieces', {filter: that.searchCondition, page: that.page + 1}).then(
      response => {
        if (response) {
          that.count = response.count
          that.sellPieces = response.datas
          that.page += 1
        }
      }
    )
  }

  preloadSellPieceDetail = (to, from, next) => {
    let that = this
    utils.restGet('/public/findSellPieces', {filter: {_id: to.params.id}}).then(
      response => {
        if (response) {
          if (response.datas.length > 0) {
            that.sellPieceDetail = new SellPiece(response.datas[0])
            next()
          } else {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
              utils.router.push({path: '/'})
            })
          }
        }
      }
    )
  }

  preloadSellPiecePreview = (to, from, next) => {
    let that = this
    utils.restGet('/public/findSellPieces', {filter: {_id: to.params.id}}).then(
      response => {
        if (response) {
          if (response.datas.length > 0) {
            that.sellPieceDetail = new SellPiece(response.datas[0])
            if (parseInt(to.params.index) >= 0 && parseInt(to.params.index) < that.sellPieceDetail.previews.length) {
              next()
            } else {
              utils.event.$emit('SHOW_MESSAGE', {code: 'B001'}, () => {
                utils.router.push({path: '/'})
              })
            }
          } else {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
              utils.router.push({path: '/'})
            })
          }
        }
      }
    )
  }
}

export default new EstateSellPieceService()



