<template>
<div class="md-layout md-gutter">
  <div class="md-layout-item md-size-100">
    <md-toolbar md-theme="light" class="md-dense md-primary">
      <h3 class="md-title">
        売出物件 ({{manager.estateSellPieceService.sellPieces.length}} / {{manager.estateSellPieceService.count}})
      </h3>
    </md-toolbar>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100" v-for="piece in manager.estateSellPieceService.sellPieces" :key="piece._id">
    <sellPieceCard :manager="manager" :object="piece"/>
  </div>
  <div class="md-layout-item md-size-100 more-container" v-if="manager.estateSellPieceService.sellPieces.length < manager.estateSellPieceService.count">
    <md-button class="md-primary more-button" @click="more()">結果をもっと表示 ({{manager.estateSellPieceService.sellPieces.length}} / {{manager.estateSellPieceService.count}})</md-button>
  </div>
</div>
</template>

<script>
import sellPieceCard from '@/components/shared/sellPieceCard'
export default {
  props: ['manager'],
  components: {
    sellPieceCard
  },
  methods: {
    more () {
      this.manager.estateSellPieceService.moreBuy()
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
}
.md-layout-item {
  margin-bottom: 20px;
}
.more-container {
  display: flex;
  justify-content: center;
}
</style>



