<template>
<md-app md-waterfall md-mode="fixed">
  <vue-headful title="ブドウさん"/>

  <md-app-toolbar class="md-primary">
    <div class="md-toolbar-row">

      <div class="md-toolbar-section-start">
        <md-tabs class="md-primary" :md-active-tab="route">
          <md-tab id="route-home" md-icon="home" md-label="ホーム" to="/"></md-tab>
          <md-tab id="route-contact" md-icon="contacts" md-label="連絡帳" to="/contact"></md-tab>
          <md-tab id="route-chat" md-icon="chat" md-label="チャット" to="/chat"></md-tab>
          <md-tab id="route-my" md-icon="accessibility" md-label="本人" to="/my"></md-tab>
        </md-tabs>
      </div>

      <div class="md-toolbar-section-end">
        <span class="user-name bd-small-hide"><md-icon>person</md-icon>{{manager.user.name}}</span>
        <md-button class="md-icon-button" @click="logout()" v-if="!manager.user.isAnonymous">
          <md-icon>power_settings_new</md-icon>
        </md-button>
        <md-button to="/login" v-else>
          ログイン
        </md-button>
      </div>

    </div>
  </md-app-toolbar>

  <md-app-content>
    <router-view :manager="manager"/>
    <detailDialog :manager="manager"/>
    <photoViewerDialog :manager="manager"/>
  </md-app-content>

</md-app>
</template>

<script>
import utils from '@/tool/utils.js'
import detailDialog from '@/components/shared/detailDialog'
import photoViewerDialog from '@/components/common/photoViewerDialog'
export default {
  props: ['manager'],
  components: {
    detailDialog,
    photoViewerDialog
  },
  computed: {
    route () {
      if (this.$route.name === 'estate' || this.$route.name === 'sellPiece' || this.$route.name === 'buyRequest' || this.$route.name === 'rentPiece' || this.$route.name === 'borrowRequest') {
        return 'route-home'
      } else if (this.$route.name === 'contact') {
        return 'route-contact'
      } else if (this.$route.name === 'chat') {
        return 'route-chat'
      } else if (this.$route.name === 'account' || this.$route.name === 'apply' || this.$route.name === 'mySell' || this.$route.name === 'mySellPiece' || this.$route.name === 'myBuy' || this.$route.name === 'myBuyRequest' || this.$route.name === 'myRent' || this.$route.name === 'myRentPiece' || this.$route.name === 'myBorrow' || this.$route.name === 'myBorrowRequest') {
        return 'route-my'
      }
    }
  },
  methods: {
    logout () {
      utils.restGet('/logout').then(
        response => {
          if (response) {
            utils.router.push({name: 'login'})
          }
        }
      )
    }
  }
}
</script>

<style scoped>
.md-app {
  width: 100%;
  height: 100%;
  position: relative;
}
.user-name {
  height: 36px;
  font-size: 16px;
  display: flex;
  align-items: center;
  padding: 5px 10px;
}
.md-button {
  margin: 0px;
}
</style>



