<template>
<div class="md-layout md-gutter">
  <div class="md-layout-item md-size-100">
    <md-toolbar md-theme="light" class="md-dense md-primary">
      <h3 class="md-title">
        貸付物件 ({{manager.estateRentPieceService.rentPieces.length}} / {{manager.estateRentPieceService.count}})
      </h3>
    </md-toolbar>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100" v-for="piece in manager.estateRentPieceService.rentPieces" :key="piece._id">
    <sellPieceCard :manager="manager" :object="piece"/>
  </div>
  <div class="md-layout-item md-size-100 more-container" v-if="manager.estateRentPieceService.rentPieces.length < manager.estateRentPieceService.count">
    <md-button class="md-primary more-button" @click="more()">結果をもっと表示 ({{manager.estateRentPieceService.rentPieces.length}} / {{manager.estateRentPieceService.count}})</md-button>
  </div>
</div>
</template>

<script>
import sellPieceCard from '@/components/shared/sellPieceCard'
// import utils from '@/tool/utils.js'
export default {
  props: ['manager'],
  components: {
    sellPieceCard
  },
  methods: {
    more () {
      this.manager.estateRentPieceService.moreRentPiece()
      // const last = this.manager.estateRentPieceService.rentPieces[this.manager.estateRentPieceService.rentPieces.length - 1]
      // utils.restGet('/public/findRentPieces', {
      //   filter: {},
      //   paging: {_id: last._id}
      // }).then(
      //   response => {
      //     if (response) {
      //       this.manager.estateRentPieceService.count = response.count
      //       this.manager.estateRentPieceService.rentPieces = response.rentPieces
      //     }
      //   }
      // )
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
}
.md-layout-item {
  margin-bottom: 20px;
}
</style>



