<template>
<div class="md-layout">
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui md-primary">建物種別</md-content></md-content>
      <p class="detail-value">{{piece.typeText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">取引態様</md-content></md-content>
      <p class="detail-value">{{piece.aspectText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">価格</md-content></md-content>
      <p class="detail-value">{{piece.priceText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">仲介手数料</md-content></md-content>
      <p class="detail-value">{{piece.marginText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">現況</md-content></md-content>
      <p class="detail-value">{{piece.availableText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">間取り</md-content></md-content>
      <p class="detail-value">{{piece.layoutText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">階層</md-content></md-content>
      <p class="detail-value">{{piece.floorText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100" v-if="piece.isApartment">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">専有面積</md-content></md-content>
      <p class="detail-value">{{piece.exclusiveAreaText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100" v-if="piece.isDetached">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">建築面積</md-content></md-content>
      <p class="detail-value">{{piece.buildingAreaText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100" v-if="piece.isDetached">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">土地面積</md-content></md-content>
      <p class="detail-value">{{piece.landAreaText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">構造</md-content></md-content>
      <p class="detail-value">{{piece.structureText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
    <div class="detail-row">
      <md-content class="detail-label"><md-content class="detail-label-ui">築年月(数)</md-content></md-content>
      <p class="detail-value">{{piece.builtText}}</p>
    </div>
  </div>
  <div class="md-layout-item md-size-100 md-layout">
    <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui">交通</md-content></md-content>
        <p class="detail-value" v-if="piece.stations != null && piece.stations.length > 0">{{piece.stations[0].station}} <span v-if="piece.stations[0].walking != null">徒歩{{piece.stations[0].walking}}分</span></p>
        <p class="detail-value" v-else> -</p>
      </div>
    </div>
    <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100" v-if="piece.stations && piece.stations.length > 1">
      <div class="detail-row" v-for="(station, index) in piece.stations" :key="station._id" v-if="index > 0">
        <md-content class="detail-label"></md-content>
        <p class="detail-value">{{station.station}} <span v-if="station.walking != null">徒歩{{station.walking}}分</span></p>
      </div>
    </div>
  </div>

  <div class="md-layout-item md-size-100 md-layout detail-table">
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        管理費
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.manageText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        修繕費
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.repairText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50">
      <div class="detail-table-header detail-table-center">
        入居可能日
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.residentableText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50">
      <div class="detail-table-header detail-table-center">
        権利態様
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.rightsText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        方位
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.directionText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        総戸数
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.householdsText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        駐車場
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.parkingText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        ペット
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.petText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        バルコニー
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.balconyText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isApartment">
      <div class="detail-table-header detail-table-center">
        エアコン
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.airConditioningText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isDetached">
      <div class="detail-table-header detail-table-center">
        建ぺい率
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.coverageText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50" v-if="piece.isDetached">
      <div class="detail-table-header detail-table-center">
        容積率
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.volumeText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50">
      <div class="detail-table-header detail-table-center">
        掲載日
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.cdateText}}
      </div>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-33 md-xsmall-size-50">
      <div class="detail-table-header detail-table-center">
        更新日
      </div>
      <div class="detail-table-cell detail-table-center">
        {{piece.udateText}}
      </div>
    </div>
  </div>
  <div class="detail-comment" v-if="piece.comment">
    {{piece.comment}}
  </div>
</div>
</template>

<script>
export default {
  props: ['manager', 'piece']
}
</script>

<style scoped lang="scss">
.detail-row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.detail-label {
  width: 90px;
  margin: 8px;
  display: flex;
  justify-content: flex-end;
}
.detail-label-ui {
  width: fit-content;
  font-size: 12px;
  padding: 0px 10px;
  border-radius: 20px;
  background: #e8e8e8;
}
.detail-value {
  width: calc(100% - 90px);
  border-bottom: 1px solid #e8e8e8;
  margin: 8px 0px;
}
.detail-table {
  padding: 10px;
}
.detail-table-header {
  background: #e8e8e8;
  border: 1px solid #ddd;
  padding: 5px;
}
.detail-table-cell {
  border: 1px solid #ddd;
  padding: 5px;
}
.detail-table-right {
  text-align: right;
}
.detail-table-center {
  text-align: center;
}
.detail-comment {
  width: 100%;
  margin: 10px;
  padding: 15px;
  color: #666;
  word-break: break-all;
  background: #e8e8e8;
  border-radius: 3px;
  min-height: 100px;
  border: 1px solid #ddd;
}
</style>



