import utils from '@/tool/utils.js'
import RentPiece from '@/store/models/rentPiece.js'

class EstateRentPieceService {
  constructor () {
    this._rentPieces = []
    this._count = 0
    this.page = 0

    this.searchCondition = {
      stations: [],
      minute: '',
      layouts: '',
      area: '',
      type: '',
      structure: '',
      age: '',
      min: '',
      max: '',
      releday: ''
    }
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get rentPieces () {
    return this._rentPieces
  }
  set rentPieces (pieces) {
    pieces.forEach(one => {
      this._rentPieces.push(new RentPiece(one))
    })
  }

  init () {
    this._rentPieces.splice(0, this._rentPieces.length)
    this._count = 0
    this.page = 0
  }

  preloadRentPiece = (to, from, next) => {
    let that = this

    utils.restGet('/public/findRentPieces', {}).then(
      response => {
        if (response) {
          that.init()
          that.count = response.count
          that.rentPieces = response.datas
          next()
        }
      }
    )
  }

  preloadRentPieceDetail = (to, from, next) => {
    let that = this
    utils.restGet('/public/findRentPieces', {filter: {_id: to.params.id}}).then(
      response => {
        if (response) {
          that.rentPieceDetail = new RentPiece(response.datas[0])
          next()
        }
      }
    )
  }

  moreRentPiece () {
    let that = this
    utils.restGet('/public/findRentPieces', {filter: that.searchCondition, page: that.page + 1}).then(
      response => {
        if (response) {
          that.count = response.count
          that.rentPieces = response.datas
          that.page += 1
        }
      }
    )
  }
}

export default new EstateRentPieceService()



