<template>
<md-card md-with-hover>
  <md-card-header>
    <div class="md-title">{{object.name}}</div>
    <div class="md-subhead">{{object.address}}</div>
  </md-card-header>

  <md-card-content>
    <div class="md-layout md-alignment-center-center">
      <div class="md-layout-item md-size-40 md-medium-size-70">
        <div @click="viewPhotos(object)">
          <photoGallery class="piece-thumbnail" :manager="manager" :photos="object.photoThumbnails"/>
        </div>
        <md-content class="piece-actions">
          <md-button class="md-primary md-raised" @click.stop="publishPiece(object)" v-if="!object.isPublishing">公開</md-button>
          <md-button class="md-primary md-raised" @click.stop="unPublishPiece(object)" v-if="object.isPublishing">公開取消</md-button>
          <md-button class="md-primary md-raised" @click.stop="editPiece(object)">編集</md-button>
          <md-button class="md-primary md-raised" @click.stop="deletePiece(object)">削除</md-button>
        </md-content>
        <md-content class="detail-comment" v-if="object.comment">
          {{object.comment}}
        </md-content>
      </div>

      <div class="md-layout-item md-size-60 md-medium-size-100">
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui md-primary">建物種別</md-content></md-content>
          <p class="detail-value">{{object.typeText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">取引態様</md-content></md-content>
          <p class="detail-value">{{object.aspectText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">価格</md-content></md-content>
          <p class="detail-value">{{object.priceText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">仲介手数料</md-content></md-content>
          <p class="detail-value">{{object.marginText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">現況</md-content></md-content>
          <p class="detail-value">{{object.availableText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">間取り</md-content></md-content>
          <p class="detail-value">{{object.layoutText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">階層</md-content></md-content>
          <p class="detail-value">{{object.floorText}}</p>
        </div>
        <div class="detail-row" v-if="object.isApartment">
          <md-content class="detail-label"><md-content class="detail-label-ui">専有面積</md-content></md-content>
          <p class="detail-value">{{object.exclusiveAreaText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <md-content class="detail-label"><md-content class="detail-label-ui">建築面積</md-content></md-content>
          <p class="detail-value">{{object.buildingAreaText}}</p>
        </div>
        <div class="detail-row" v-if="object.isDetached">
          <md-content class="detail-label"><md-content class="detail-label-ui">土地面積</md-content></md-content>
          <p class="detail-value">{{object.landAreaText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">構造</md-content></md-content>
          <p class="detail-value">{{object.structureText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">築年月/数</md-content></md-content>
          <p class="detail-value">{{object.builtText}}</p>
        </div>
        <div class="detail-row">
          <md-content class="detail-label"><md-content class="detail-label-ui">交通</md-content></md-content>
          <p class="detail-value" v-if="object.stations != null && object.stations.length > 0">{{object.stations[0].station}} <span v-if="object.stations[0].walking != null">徒歩{{object.stations[0].walking}}分</span></p>
          <p class="detail-value" v-else> -</p>
        </div>
        <template v-if="object.stations && object.stations.length > 1">
          <div class="detail-row" v-for="(station, index) in object.stations" :key="station._id" v-if="index > 0">
            <md-content class="detail-label"></md-content>
            <p class="detail-value">{{station.station}} <span v-if="station.walking != null">徒歩{{station.walking}}分</span></p>
          </div>
        </template>
      </div>
    </div>
  </md-card-content>

</md-card>
</template>

<script>
import photoGallery from '@/components/common/photoGallery'
import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'object'],
  components: {
    photoGallery
  },
  methods: {
    publishPiece (piece) {
      let that = this

      piece.isPublishing = 1
      that.manager.mySellService.publishSellPiece(piece).then((err) => {
        if (err) {
          piece.isPublishing = false
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
            that.$router.push({name: 'mySell'})
          })
        }
      })
    },
    unPublishPiece (piece) {
      let that = this

      piece.isPublishing = 1
      that.manager.mySellService.unPublishSellPiece(piece).then((err) => {
        if (err) {
          piece.isPublishing = false
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
            that.$router.push({name: 'mySell'})
          })
        }
      })
    },
    viewPhotos (piece) {
      if (piece.photos && piece.photos.length > 0) utils.event.$emit('SHOW_PHOTOVIEWER', piece.photos)
    },
    editPiece (piece) {
      if (piece.id) this.$router.push({path: '/my/sellPiece/' + piece.id})
    },
    deletePiece (piece) {
      let that = this
      if (piece.id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I003'}, () => {
          that.manager.mySellService.deleteSellPiece(piece, function () {
            utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
              that.manager.mySellService.preloadMySell(null, null, function () {
                that.$router.push({name: 'mySell'})
              })
            })
          })
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}
.piece-thumbnail {
  cursor: pointer;
}
.piece-thumbnail:hover {
  opacity: 0.9;
}
.piece-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0px;
  .md-button {
    margin: 6px;
  }
}
.detail-row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.detail-label {
  width: 90px;
  margin: 8px;
  display: flex;
  justify-content: flex-end;
}
.detail-label-ui {
  width: fit-content;
  font-size: 12px;
  padding: 0px 10px;
  border-radius: 20px;
  background: #e8e8e8;
}
.detail-value {
  width: calc(100% - 90px);
  border-bottom: 1px solid #e8e8e8;
  margin: 8px 0px;
  padding: 0px 10px;
}
.detail-comment {
  width: 100%;
  padding: 15px;
  color: #666;
  word-break: break-all;
  background: #e8e8e8;
  border-radius: 3px;
  border: 1px solid #ddd;
}
</style>



