<template>
<div>
  <p class="md-title">物件情報</p>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field>
        <label>権利形態</label>
        <md-select v-model="manager.myRentService.editRentPiece.rights">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(rights, key) in manager.const.rights" :key="key">{{rights}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-datepicker v-model="residentable">
        <label>入居可能日</label>
      </md-datepicker>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentService.editRentPiece.isApartment">
      <md-field>
        <label>方位</label>
        <md-select v-model="manager.myRentService.editRentPiece.direction">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(direction, key) in manager.const.direction" :key="key">{{direction}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentService.editRentPiece.isApartment">
      <md-field :class="{'md-invalid': !isHouseholdsValid}">
        <label>総戸数</label>
        <md-input v-model="manager.myRentService.editRentPiece.households" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentService.editRentPiece.isDetached">
      <md-field :class="{'md-invalid': !isCoverageValid}">
        <label>建ぺい率</label>
        <md-input v-model="manager.myRentService.editRentPiece.coverage" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentService.editRentPiece.isDetached">
      <md-field :class="{'md-invalid': !isVolumeValid}">
        <label>容積率</label>
        <md-input v-model="manager.myRentService.editRentPiece.volume" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left" v-if="manager.myRentService.editRentPiece.isApartment">
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentService.editRentPiece.parking" class="md-primary">駐車場</md-checkbox>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentService.editRentPiece.balcony" class="md-primary">バルコニー</md-checkbox>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentService.editRentPiece.pet" class="md-primary">ペット</md-checkbox>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentService.editRentPiece.airConditioning" class="md-primary">エアコン</md-checkbox>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left" v-if="manager.myRentService.editRentPiece.isApartment">
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field :class="{'md-invalid': !isManageValid}">
        <label>管理費</label>
        <md-input v-model="manager.myRentService.editRentPiece.manage" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field :class="{'md-invalid': !isRepairValid}">
        <label>修繕費</label>
        <md-input v-model="manager.myRentService.editRentPiece.repair" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
  </div>
</div>
</template>

<script>
// import moment from 'moment'
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager'],
  data: () => ({
    residentable: null
  }),
  created () {
    this.residentable = manager.myRentService.editRentPiece._residentable ? manager.myRentService.editRentPiece._residentable.format('YYYY-MM-DD') : null
  },
  computed: {
    isHouseholdsValid () {
      if (!manager.myRentService.editRentPiece.households) return true
      if (manager.myRentService.editRentPiece.households <= 0) return false
      return true
    },
    isManageValid () {
      if (!manager.myRentService.editRentPiece.manage) return true
      if (manager.myRentService.editRentPiece.manage <= 0) return false
      return true
    },
    isRepairValid () {
      if (!manager.myRentService.editRentPiece.repair) return true
      if (manager.myRentService.editRentPiece.repair <= 0) return false
      return true
    },
    isCoverageValid () {
      if (!manager.myRentService.editRentPiece.coverage) return true
      if (manager.myRentService.editRentPiece.coverage <= 0) return false
      return true
    },
    isVolumeValid () {
      if (!manager.myRentService.editRentPiece.volume) return true
      if (manager.myRentService.editRentPiece.volume <= 0) return false
      return true
    }
  },
  methods: {
    validate () {
      if (!this.isHouseholdsValid || !this.isManageValid || !this.isRepairValid || !this.isCoverageValid || !this.isVolumeValid) {
        return '入力不正です。'
      }
      manager.myRentService.editRentPiece.residentable = this.residentable
      // TODO: md bug
      if (manager.myRentService.editRentPiece._residentable.year() === 1970) manager.myRentService.editRentPiece.residentable = null
      return null
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



