<template>
<md-content class="content-wrapper md-scrollbar" ref="contentWarper">
  <vue-headful title="ブドウさん - 賃貸物件登録"/>
  <div class="md-headline" v-if="manager.myRentService.editRentPiece.id">賃貸物件登録</div>
  <div class="md-headline" v-else>賃貸物件編集</div>
  <md-steppers :md-active-step.sync="active" md-vertical md-linear>
    <md-step id="step1" md-label="基本情報" :md-error="firstStepError" :md-editable="false" :md-done.sync="step1">
      <myRentPieceBase ref="baseInfo" :manager="manager"/>
      <md-button class="md-raised md-primary" @click="cancel()">取消</md-button>
      <md-button class="md-raised md-primary" @click="setFirstStepDone()">次へ</md-button>
    </md-step>
    <md-step id="step2" md-label="詳細情報" :md-error="secondStepError" :md-editable="false" :md-done.sync="step2">
      <myRentPieceDetail ref="detailInfo" :manager="manager"/>
      <md-button class="md-raised md-primary" @click="back('step2', 'step1')">戻る</md-button>
      <md-button class="md-raised md-primary" @click="setSecondStepDone()">次へ</md-button>
    </md-step>
    <md-step id="step3" md-label="物件画像" :md-editable="false" :md-done.sync="step3">
      <myRentPiecePicture ref="pictures" :manager="manager"/>
      <md-button class="md-raised md-primary" @click="back('step3', 'step2')">戻る</md-button>
      <md-button class="md-raised md-primary" @click="save()">保存</md-button>
    </md-step>
  </md-steppers>
</md-content>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import myRentPieceBase from './myRentPieceBase'
import myRentPieceDetail from './myRentPieceDetail'
import myRentPiecePicture from './myRentPiecePicture'
export default {
  props: ['manager'],
  components: {
    myRentPieceBase,
    myRentPieceDetail,
    myRentPiecePicture
  },
  data: () => ({
    active: 'step1',
    step1: false,
    step2: false,
    step3: false,
    firstStepError: null,
    secondStepError: null
  }),
  methods: {
    cancel () {
      this.$router.push({name: 'myRent'})
    },
    setFirstStepDone () {
      this.firstStepError = this.$refs.baseInfo.validate()
      this.$refs.contentWarper.$el.scrollTop = 0
      if (!this.firstStepError) {
        this.step1 = true
        this.active = 'step2'
      }
    },
    setSecondStepDone () {
      this.secondStepError = this.$refs.detailInfo.validate()
      this.$refs.contentWarper.$el.scrollTop = 0
      if (!this.secondStepError) {
        this.step2 = true
        this.active = 'step3'
      }
    },
    back (current, pre) {
      this[current] = false
      this[pre] = false
      this.active = pre
    },
    save () {
      const that = this
      that.$refs.pictures.upload(() => {
        if (!manager.myRentService.editRentPiece.id) {
          manager.myRentService.insertRentPiece(manager.myRentService.editRentPiece, () => {
            that.$router.push({path: '/my/rent'})
          })
        } else {
          manager.myRentService.updateRentPiece(manager.myRentService.editRentPiece, () => {
            that.$router.push({path: '/my/rent'})
          })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.content-wrapper {
  height: 100%;
  overflow: auto;
  padding: 20px;
}
.md-button {
  margin-top: 40px;
}
</style>



