<template>
<md-content class="content-wrapper md-scrollbar">
  <vue-headful title="ブドウさん - 賃貸要望管理"/>
  <div class="subTitle">賃貸要望 <span v-if="isCreateNew">新規</span></div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-100">
        <label class="houseType">建物種別</label>
        <md-radio v-model="manager.myBorrowService.editBorrowRequest.type" value="1">{{manager.const.type[1]}}</md-radio>
        <md-radio v-model="manager.myBorrowService.editBorrowRequest.type" value="2">{{manager.const.type[2]}}</md-radio>
        <md-radio v-model="manager.myBorrowService.editBorrowRequest.type" value="3">{{manager.const.type[3]}}</md-radio>
        <md-radio v-model="manager.myBorrowService.editBorrowRequest.type" value="4">{{manager.const.type[4]}}</md-radio>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field>
        <label>中古・新築</label>
        <md-select v-model="manager.myBorrowService.editBorrowRequest.isnew">
          <md-option value="">ー</md-option>
          <md-option value="1">中古のみ</md-option>
          <md-option value="2">新築のみ</md-option>
        </md-select>
      </md-field>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <md-field>
        <label>価格From</label>
        <md-input v-model="manager.myBorrowService.editBorrowRequest.priceFrom" type="number"></md-input>
        <span class="md-helper-text">単位・万</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <md-field>
        <label>価格To</label>
        <md-input v-model="manager.myBorrowService.editBorrowRequest.priceTo" type="number"></md-input>
        <span class="md-helper-text">単位・万</span>
      </md-field>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field>
        <label for="nationality">国籍</label>
        <md-input ref="nationality" v-model="manager.myBorrowService.editBorrowRequest.nationality"></md-input>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field>
        <label for="profession">職業</label>
        <md-input ref="profession" v-model="manager.myBorrowService.editBorrowRequest.profession"></md-input>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33e md-xsmall-size-100">
      <md-field>
        <label>年齢代数</label>
        <md-select ref="generation" v-model="manager.myBorrowService.editBorrowRequest.generation">
          <md-option value="">ー</md-option>
          <md-option value="1">１０代</md-option>
          <md-option value="2">２０代</md-option>
          <md-option value="3">３０代</md-option>
          <md-option value="4">４０代</md-option>
          <md-option value="5">５０代</md-option>
          <md-option value="6">６０代</md-option>
          <md-option value="7">７０代</md-option>
        </md-select>
      </md-field>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field>
        <label>路線</label>
        <md-input ref="profession" v-model="manager.myBorrowService.editBorrowRequest.line"></md-input>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field>
        <label>駅</label>
        <md-input ref="station" v-model="manager.myBorrowService.editBorrowRequest.station"></md-input>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field>
        <label>駅から所要時間</label>
        <md-input ref="requiredTime" v-model="manager.myBorrowService.editBorrowRequest.requiredTime"></md-input>
        <span class="md-helper-text">単位・分</span>
      </md-field>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-95">
      <md-field>
        <label>コメント</label>
        <md-textarea v-model="manager.myBorrowService.editBorrowRequest.comment"></md-textarea>
        <span class="md-helper-text">要望に関する補足情報</span>
      </md-field>
    </div>
  </div>
  <md-button class="md-raised md-primary" @click="cancel()">取消</md-button>
  <md-button class="md-raised md-primary" @click="preview">プレビュー</md-button>
  <md-button class="md-raised md-primary" @click="save()">保存</md-button>
</md-content>
</template>

<script>
import utils from '@/tool/utils.js'
import BorrowRequest from '@/store/models/borrowRequest.js'
export default {
  props: ['manager'],
  components: {
    BorrowRequest
  },
  data: () => ({
    inValidObj: '',
    isCreateNew: true,
    // center: {lat: 35.416645, lng: 140.042037},
    markers: [{
      position: {lat: 35.416645, lng: 140.042037}
    }],
    lat: 35.416645,
    lng: 140.042037
  }),
  computed: {
    center () {
      return this.markers[0].position
    }
  },
  watch: {
    lat () {
      console.log(this.lat)
      this.markers[0].position.lat = parseFloat(this.lat)
    },
    lng () {
      console.log(this.lng)
      this.markers[0].position.lng = parseFloat(this.lng)
    }
  },
  created () {
    let that = this
    if (that.manager.myBorrowService.editBorrowRequest.id) {
      this.isCreateNew = false
    }
  },
  methods: {
    isMinuteValid (index) {
      if (this.stations[index] === null || this.stations[index][1] === null || this.stations[index][1] === '') return true
      if (parseFloat(this.stations[index][1]) <= 0) return false
      if (Math.round(parseFloat(this.stations[index][1])) === parseFloat(this.stations[index][1])) return true
      return false
    },
    cancel () {
      this.$router.go(-1)
    },
    preview () {
      let that = this

      utils.event.$emit('SHOW_DETAIL', 'myBorrowRequestCard', that.manager.myBorrowService.editBorrowRequest, true)
    },
    save () {
      let that = this
      if (that.isCreateNew) {
        that.manager.myBorrowService.insertBorrowRequest(that.manager.myBorrowService.editBorrowRequest, () => {
          that.$router.push({path: '/my/myBorrow'})
        })
      } else {
        that.manager.myBorrowService.updateBorrowRequest(that.manager.myBorrowService.editBorrowRequest, () => {
          that.$router.push({path: '/my/myBorrow'})
        })
      }
    },
    onSaveCompleted (resposne) {
      let that = this
      utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
        that.$router.push({name: 'myBorrow'})
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.content-wrapper {
  height: 100%;
  overflow: auto;
  border-bottom: 1px solid rgba(0, 0, 0, .12);
  padding: 20px;
}
textarea {
  resize: none !important;
}
.houseType {
  margin-right: 20px;
}
.subTitle{
  font-size: 30px;
  font-weight: 500;
  padding: 10px;
}
.gmap {
  width: 100%;
  height: 400px
}
</style>



