<template>
<md-card>
  <md-card-content>
    <div class="md-layout md-gutter md-alignment-left">
      <div class="md-layout-item md-size-75 md-small-size-100">
        <bdsMdChips v-model="stations" :md-format="filterStation" :mdSelections="selections" mdHelper="線路名もしくは駅名で曖昧検索" mdPlaceholder="最寄り駅"></bdsMdChips>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>徒歩時間</label>
          <md-select v-model="minute">
            <md-option value="">指定なし</md-option>
            <md-option value="1">1分以内</md-option>
            <md-option value="5">5分以内</md-option>
            <md-option value="10">10分以内</md-option>
            <md-option value="15">15分以内</md-option>
            <md-option value="20">20分以内</md-option>
            <md-option value="30">30分以内</md-option>
          </md-select>
          <span class="md-helper-text">駅から物件までの徒歩時間</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>間取り</label>
          <md-select v-model="layouts" multiple>
            <md-option value="1R">1R</md-option>
            <md-option value="1DK">1DK</md-option>
            <md-option value="2DK">2DK</md-option>
            <md-option value="2LDK">2LDK</md-option>
            <md-option value="3DK">3DK</md-option>
            <md-option value="3LDK">3LDK</md-option>
            <md-option value="4DK">4DK</md-option>
            <md-option value="4LDK">4LDK</md-option>
            <md-option value="5DK">5DK</md-option>
            <md-option value="5LDK">5LDK以上</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>面積</label>
          <md-select v-model="area">
            <md-option value="">指定なし</md-option>
            <md-option value="20">20m²以上</md-option>
            <md-option value="30">30m²以上</md-option>
            <md-option value="40">40m²以上</md-option>
            <md-option value="50">50m²以上</md-option>
            <md-option value="60">60m²以上</md-option>
            <md-option value="70">70m²以上</md-option>
            <md-option value="80">80m²以上</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>建物種別</label>
          <md-select v-model="type">
            <md-option value="">指定なし</md-option>
            <md-option value="1">マンション</md-option>
            <md-option value="2">一戸建</md-option>
            <md-option value="3">アパート</md-option>
            <md-option value="4">その他</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>建物構造</label>
          <md-select v-model="structure">
            <md-option value="">指定なし</md-option>
            <md-option value="1">鉄筋系</md-option>
            <md-option value="2">鉄骨系</md-option>
            <md-option value="3">木造</md-option>
            <md-option value="4">その他</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>築年数</label>
          <md-select v-model="age">
            <md-option value="">指定なし</md-option>
            <md-option value="1">1年以内</md-option>
            <md-option value="3">3年以内</md-option>
            <md-option value="5">5年以内</md-option>
            <md-option value="7">7年以内</md-option>
            <md-option value="10">10年以内</md-option>
            <md-option value="15">15年以内</md-option>
            <md-option value="20">20年以内</md-option>
            <md-option value="25">25年以内</md-option>
            <md-option value="30">30年以内</md-option>
            <md-option value="40">40年以内</md-option>
            <md-option value="50">50年以内</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>価格 From:</label>
          <md-select v-model.number="min">
            <md-option value="">下限なし</md-option>
            <md-option value="500">500万円</md-option>
            <md-option value="1000">1000万円</md-option>
            <md-option value="1500">1500万円</md-option>
            <md-option value="2000">2000万円</md-option>
            <md-option value="2500">2500万円</md-option>
            <md-option value="3000">3000万円</md-option>
            <md-option value="3500">3500万円</md-option>
            <md-option value="4000">4000万円</md-option>
            <md-option value="4500">4500万円</md-option>
            <md-option value="5000">5000万円</md-option>
            <md-option value="5500">5500万円</md-option>
            <md-option value="6000">6000万円</md-option>
            <md-option value="7000">7000万円</md-option>
            <md-option value="8000">8000万円</md-option>
            <md-option value="9000">9000万円</md-option>
            <md-option value="10000">10000万円</md-option>
            <md-option value="15000">15000万円</md-option>
            <md-option value="20000">20000万円</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>価格 To:</label>
          <md-select v-model.number="max">
            <md-option value="">上限なし</md-option>
            <md-option value="500">500万円</md-option>
            <md-option value="1000">1000万円</md-option>
            <md-option value="1500">1500万円</md-option>
            <md-option value="2000">2000万円</md-option>
            <md-option value="2500">2500万円</md-option>
            <md-option value="3000">3000万円</md-option>
            <md-option value="3500">3500万円</md-option>
            <md-option value="4000">4000万円</md-option>
            <md-option value="4500">4500万円</md-option>
            <md-option value="5000">5000万円</md-option>
            <md-option value="5500">5500万円</md-option>
            <md-option value="6000">6000万円</md-option>
            <md-option value="7000">7000万円</md-option>
            <md-option value="8000">8000万円</md-option>
            <md-option value="9000">9000万円</md-option>
            <md-option value="10000">10000万円</md-option>
            <md-option value="15000">15000万円</md-option>
            <md-option value="20000">20000万円</md-option>
          </md-select>
        </md-field>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-33 md-xsmall-size-100">
        <md-field>
          <label>情報公開日</label>
          <md-select v-model="releday">
            <md-option value="0">指定なし</md-option>
            <md-option value="1">本日公開</md-option>
            <md-option value="3">3日以内に公開</md-option>
            <md-option value="7">一週間以内に公開</md-option>
            <md-option value="30">30日以内に公開</md-option>
          </md-select>
        </md-field>
      </div>
    </div>
  </md-card-content>

  <md-card-actions>
    <md-button class="md-primary md-raised" @click.stop="search">
      <div class="button-text">
        <md-icon>search</md-icon><p>物件を検索</p>
      </div>
    </md-button>
  </md-card-actions>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import bdsMdChips from '@/components/common/vue-material/bdsMdChips'
export default {
  props: ['manager'],
  components: {
    bdsMdChips
  },
  data: () => ({
    selections: [],
    stations: [],
    minute: '',
    layouts: [],
    area: '',
    type: '',
    structure: '',
    age: '',
    min: '',
    max: '',
    releday: ''
  }),
  created () {
    this.selections.splice(0, this.selections.length)
    for (let line in manager.const.station) {
      manager.const.station[line].forEach(station => {
        this.selections.push({value: station.sn, name: line + '-' + station.sn})
      })
    }
  },
  methods: {
    filterStation (input) {
      if (!input) return
      let selection = null
      this.selections.some(one => {
        const isAdded = this.stations.some(station => {
          if (station === one.value) return true
        })
        if (!isAdded && one.name.indexOf(input) >= 0) {
          selection = one.value
          return true
        }
      })
      return selection
    },
    search: function () {
      let that = this

      let searchCondition = {
        stations: that.stations,
        minute: that.minute,
        layouts: that.layouts,
        area: that.area,
        type: that.type,
        structure: that.structure,
        age: that.age,
        min: that.min,
        max: that.max,
        releday: that.releday
      }
      that.manager.estateSellPieceService.searchCondition = searchCondition

      utils.restGet('/public/findSellPieces', {filter: that.manager.estateSellPieceService.searchCondition}).then(
        response => {
          if (response) {
            that.manager.estateSellPieceService.init()
            that.manager.estateSellPieceService.count = response.count
            that.manager.estateSellPieceService.sellPieces = response.datas
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px 0px 20px 0px;
}
.item-lable {
  opacity: 0.54;
  font-size: 16px;
}
.range-frame {
  width: 100%;
  display: flex;
  label {
    width: 50px;
  }
}
.item-input {
  width: 100%;
}

.hideDetail{
  display: none;
}
</style>



