<template>
<md-content class="content-wrapper md-scrollbar" ref="contentWarper">
  <vue-headful title="ブドウさん - 賃貸要望管理"/>
  <div class="subtitle-warper">
    <span class="subTitle">賃貸要望管理</span>
    <md-button class="md-icon-button md-raised md-accent button-createnew" to="/my/borrowRequest/new">
      <md-icon>add</md-icon>
    </md-button>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-100">
      <md-table>
        <pagination
          :md-page-size="manager.myBorrowService.pagesize"
          :md-total="manager.myBorrowService.count"
          :md-page="manager.myBorrowService.pageindex"
          md-label="Rows"
          md-separator="of"
          :md-page-options="[5, 10, 25, 50]"
          @goToPrevious="goToPrevious"
          @goToPage="goToPage"
          @goToNext="goToNext">
        </pagination>
      </md-table>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="request in manager.myBorrowService.borrowRequests" :key="request._id">
      <myBorrowRequestCard :manager="manager" :object="request"/>
    </div>
    <div class="md-layout-item md-size-100">
      <md-table>
        <pagination
          :md-page-size="manager.myBorrowService.pagesize"
          :md-total="manager.myBorrowService.count"
          :md-page="manager.myBorrowService.pageindex"
          md-label="Rows"
          md-separator="of"
          :md-page-options="[5, 10, 25, 50]"
          @goToPrevious="goToPrevious"
          @goToPage="goToPage"
          @goToNext="goToNext">
        </pagination>
      </md-table>
    </div>
  </div>
</md-content>
</template>

<script>
import pagination from '@/components/common/pagination'
import myBorrowRequestCard from '@/components/shared/myBorrowRequestCard'
export default {
  props: ['manager'],
  components: {
    myBorrowRequestCard,
    pagination
  },
  methods: {
    goToPrevious: function () {
      let that = this
      let toPage = this.manager.myBorrowService.pageindex - 1
      this.manager.myBorrowService.pageindex = toPage
      this.manager.myBorrowService.findBorrowRequest({}, toPage).then(() => {
        that.$nextTick(() => {
          that.$refs.contentWarper.$el.scrollTop = 0
        })
      })
    },
    goToPage: function (e) {
      let that = this
      this.manager.myBorrowService.pageindex = e.page
      this.manager.myBorrowService.findBorrowRequest({}, e.page).then(() => {
        that.$nextTick(() => {
          that.$refs.contentWarper.$el.scrollTop = 0
        })
      })
    },
    goToNext: function () {
      let that = this
      let toPage = this.manager.myBorrowService.pageindex + 1
      this.manager.myBorrowService.pageindex = toPage
      this.manager.myBorrowService.findBorrowRequest({}, toPage).then(() => {
        that.$nextTick(() => {
          that.$refs.contentWarper.$el.scrollTop = 0
        })
      })
    }
  }
}
</script>

<style scoped>
.content-wrapper {
  height: 100%;
  overflow: auto;
  padding: 20px;
}
.subtitle-warper{
  position: relative;
  padding: 15px;
}
.subTitle{
  font-size: 30px;
  font-weight: 500;
}
.button-createnew{
  position: absolute;
  top: 0;
  right: 50px;
}
.md-layout-item{
  margin-bottom: 10px;
}
</style>



