import moment from 'moment'
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import Contact from './contact.js'

class RentPiece {
  constructor (data) {
    this._id = data._id
    this.isPublishing = data.isPublishing || 0

    // ----------------------------
    // 共通属性
    // ----------------------------

    // 業者
    this.contact = null
    if (data.contact) this.contact = new Contact(data.contact)

    this.contactID = null
    if (data.contactID) this.contactID = new Contact(data.contactID)

    // 写真
    this.photos = data.photos || []
    this.photoThumbnails = data.photoThumbnails || []
    // VRプレビュー
    this.previews = data.previews || []
    this.previewThumbnails = data.previewThumbnails || []
    // 間取り図
    this.floorPlans = data.floorPlans || []
    this.floorPlanThumbnails = data.floorPlanThumbnails || []
    // 間取り
    this.layout = data.layout || null
    // 物件名
    this.name = data.name || null
    // アドレス
    this.address = data.address || null
    // 建物種別（マンション・アパート・一戸建・その他）
    this._type = '1'
    if (data.type) this.type = data.type
    // 建物構造（鉄筋・鉄骨・木造・その他）
    this._structure = null
    if (data.structure) this.structure = data.structure
    // 築年月
    this._built = null
    if (data.built) this.built = data.built
    // 賃料
    this._price = null
    if (data.price != null) this.price = data.price
    // 値引き
    this._discount = null
    if (data.discount != null) this._discount = data.discount
    // 保証金
    this._deposit = null
    if (data.deposit != null) this._deposit = data.deposit
    // 礼金
    this._keymoney = null
    if (data.keymoney != null) this._keymoney = data.keymoney
    // 損保金額
    this._insuraneFees = null
    if (data.insuraneFees != null) this._insuraneFees = data.insuraneFees
    // 損保期間
    this._insuranePeroid = null
    if (data.insuranePeroid != null) this._insuranePeroid = data.insuranePeroid

    // 引き渡し TODO
    this._delivery = data.delivery || []
    // 契約期間
    this.contractPeroid = data.contractPeroid || null
    // 駅・線路・徒歩時間
    this.stations = data.stations || []
    // 階建て
    this.story = data.story == null ? null : parseInt(data.story)
    // 取引態様
    this._aspect = null
    if (data.aspect) this.aspect = data.aspect

    // 仲介手数料
    this._marginFee = null
    if (data.marginFee != null) this.marginFee = data.marginFee
    this._marginPercent = null
    if (data.marginPercent != null) this.marginPercent = data.marginPercent
    // 備考
    this.comment = data.comment || null
    // 現況
    this.available = data.available || true
    // 入居可能日
    this._residentable = null
    if (data.residentable) this.residentable = data.residentable
    // 掲載日
    this._cdate = null
    if (data.cdate) this.cdate = data.cdate
    // 更新日
    this._udate = null
    if (data.udate) this.udate = data.udate

    // ----------------------------
    // マンション・アパートの場合
    // ----------------------------

    // 駐車場有無
    this.parking = data.parking || null
    // 専有面積
    this.exclusiveArea = data.exclusiveArea == null ? null : parseFloat(data.exclusiveArea)
    // 階層
    this.floor = data.floor == null ? null : parseInt(data.floor)
    // 方位
    this._direction = null
    if (data.direction) this.direction = data.direction
    // 総戸数
    this.households = data.households == null ? null : parseInt(data.households)
    // 管理費・修繕費・共益費
    this._manage = null
    if (data.manage != null) this.manage = data.manage
    // バルコニー有無
    this.balcony = data.balcony || null

    // ----------------------------
    // 一戸建・その他の場合
    // ----------------------------

    // 建ぺい率
    this._coverage = null
    if (data.coverage != null) this.coverage = data.coverage
    // 容積率
    this._volume = null
    if (data.volume != null) this.volume = data.volume
    // 建物面積
    this.buildingArea = data.buildingArea == null ? null : parseFloat(data.buildingArea)
    // 土地面積
    this.landArea = data.landArea == null ? null : parseFloat(data.landArea)
  }

  get id () {
    return this._id
  }
  get isPublishing () {
    return !!this._isPublishing
  }
  set isPublishing (data) {
    this._isPublishing = data ? 1 : 0
  }
  get type () {
    return this._type
  }
  get typeText () {
    return this._type != null ? manager.const.type[this._type] : '-'
  }
  set type (data) {
    this._type = data
  }
  get structure () {
    return this._structure
  }
  get structureText () {
    return this._structure != null ? manager.const.structure[this._structure] : '-'
  }
  set structure (data) {
    this._structure = data
  }
  get built () {
    return this._built ? this._built.valueOf() : null
  }
  get builtText () {
    return this._built ? this._built.format('YYYY年MM月') + '(' + this.age + ')' : '-'
  }
  get builtInputText () {
    return this._built ? this._built.format('YYYYMM') : ''
  }
  set built (data) {
    this._built = moment(data)
  }
  get price () {
    return this._price == null ? null : parseInt(this._price)
  }
  get priceText () {
    return this._price != null ? utils.formatHugeMoney(this._price) : '-'
  }
  set price (data) {
    this._price = parseInt(data)
  }
  get discount () {
    return this._discount == null ? null : parseInt(this._discount)
  }
  set discount (data) {
    this._discount = parseInt(data)
  }
  get deposit () {
    return this._deposit == null ? null : parseInt(this._deposit)
  }
  set deposit (data) {
    this._deposit = parseInt(data)
  }
  get keymoney () {
    return this._keymoney == null ? null : parseInt(this._keymoney)
  }
  set keymoney (data) {
    this._keymoney = parseInt(data)
  }
  get insuraneFees () {
    return this._insuraneFees == null ? null : parseInt(this._insuraneFees)
  }
  set insuraneFees (data) {
    this._insuraneFees = parseInt(data)
  }
  get insuranePeroid () {
    return this._insuranePeroid == null ? null : parseInt(this._insuranePeroid)
  }
  set insuranePeroid (data) {
    this._insuranePeroid = parseInt(data)
  }
  get aspect () {
    return this._aspect
  }
  get aspectText () {
    return this._aspect != null ? manager.const.aspect[this._aspect] : '-'
  }
  set aspect (data) {
    this._aspect = data
  }
  get marginFee () {
    return this._marginFee == null ? null : parseInt(this._marginFee)
  }
  get marginFeeText () {
    return this._marginFee != null ? utils.formatHugeMoney(this._marginFee) : '-'
  }
  set marginFee (data) {
    this._marginFee = parseInt(data)
  }
  get marginPercent () {
    return this._marginPercent == null ? null : parseFloat(this._marginPercent)
  }
  get marginPercentText () {
    return this._marginPercent != null ? utils.formatPercentage(this._marginPercent) : '-'
  }
  set marginPercent (data) {
    this._marginPercent = parseFloat(data)
  }
  get availableText () {
    return this.available ? '○' : '×'
  }
  get residentable () {
    return this._residentable ? this._residentable.valueOf() : null
  }
  get residentableText () {
    return this._residentable ? this._residentable.format('YYYY年MM月DD日') : '-'
  }
  set residentable (data) {
    this._residentable = moment(data)
  }
  get parkingText () {
    return this.parking ? '有' : '無'
  }
  // 専有面積
  get exclusiveAreaText () {
    return this.exclusiveArea ? utils.formatNumber(this.exclusiveArea) + 'm²' : null
  }
  // 階層情報
  get floorText () {
    let text = '-'
    if (this.isApartment) {
      if (this.floor > 0) {
        text = '' + this.floor + '階目'
      }
      if (this.story > 0) {
        if (text !== '-') {
          text += ' / '
        } else {
          text = ''
        }
        text += this.story + '階建'
      }
    } else if (this.isDetached) {
      if (this.story > 0) {
        text = '' + this.story + '階建'
      }
    }
    return text
  }
  get direction () {
    return this._direction
  }
  get directionText () {
    return this._direction != null ? manager.const.direction[this._direction] : '-'
  }
  set direction (data) {
    this._direction = data
  }
  get householdsText () {
    return this.households == null ? '-' : utils.formatNumber(this.households)
  }
  get manage () {
    return this._manage == null ? null : parseInt(this._manage)
  }
  get manageText () {
    return this._manage != null ? utils.formatHugeMoney(this._manage) : '-'
  }
  set manage (data) {
    this._manage = parseInt(data)
  }
  get balconyText () {
    return this.balcony ? '有' : '無'
  }
  get coverage () {
    return this._coverage == null ? null : parseFloat(this._coverage)
  }
  get coverageText () {
    return this._coverage != null ? utils.formatPercentage(this._coverage) : '-'
  }
  set coverage (data) {
    this._coverage = parseFloat(data)
  }
  get volume () {
    return this._volume == null ? null : parseFloat(this._volume)
  }
  get volumeText () {
    return this._volume != null ? utils.formatPercentage(this._volume) : '-'
  }
  set volume (data) {
    this._volume = parseFloat(data)
  }
  // 建物面積
  get buildingAreaText () {
    return this.buildingArea ? utils.formatNumber(this.buildingArea) + 'm²' : null
  }
  // 土地面積
  get landAreaText () {
    return this.landArea ? utils.formatNumber(this.landArea) + 'm²' : null
  }
  get cdate () {
    return this._cdate ? this._cdate.valueOf() : null
  }
  get cdateText () {
    return this._cdate != null ? this._cdate.format('YYYY年MM月DD日') : '-'
  }
  set cdate (data) {
    this._cdate = moment(data)
  }
  get udate () {
    return this._udate ? this._udate.valueOf() : null
  }
  get udateText () {
    return this._udate != null ? this._udate.format('YYYY年MM月DD日') : '-'
  }
  set udate (data) {
    this._udate = moment(data)
  }
  // 築年数
  get age () {
    if (this._built) {
      const years = moment().diff(this._built, 'year')
      const months = moment().diff(this._built, 'month') - (years * 12)
      return '' + years + '年' + months + '月前'
    } else {
      return '-'
    }
  }
  // 手数料
  get marginText () {
    let text = ''
    if (this.marginPercent) {
      text += '売買代金の' + this.marginPercentText
    }
    if (this.marginFee) {
      if (text !== '') {
        text += ' + '
      }
      text += this.marginFeeText
    }
    return text
  }
  get isApartment () {
    return (this.type === '1' || this.type === '2')
  }
  get isDetached () {
    return (this.type === '3' || this.type === '4')
  }
  // 交通情報
  setStations (stations) {
    if (stations) {
      this.stations.splice(0, this.stations.length)
      stations.forEach(one => {
        if (one.station && one.walking) {
          this.stations.push({
            station: one.station,
            walking: parseInt(one.walking)
          })
        }
      })
    }
  }

  toJSON () {
    return {
      _id: this.id,
      isPublishing: this.isPublishing,
      contactID: this.contactID,
      photos: this.photos,
      photoThumbnails: this.photoThumbnails,
      previews: this.previews,
      previewThumbnails: this.previewThumbnails,
      floorPlans: this.floorPlans,
      floorPlanThumbnails: this.floorPlanThumbnails,
      layout: this.layout,
      name: this.name,
      address: this.address,
      type: this.type,
      structure: this.structure,
      built: this.built,
      price: this.price,
      discount: this.discount,
      deposit: this.deposit,
      keymoney: this.keymoney,
      insuraneFees: this.insuraneFees,
      insuranePeroid: this.insuranePeroid,
      delivery: this._delivery,
      contractPeroid: this.contractPeroid,
      stations: this.stations,
      story: this.story,
      aspect: this.aspect,
      marginFee: this.marginFee,
      marginPercent: this.marginPercent,
      comment: this.comment,
      available: this.available,
      residentable: this.residentable,
      parking: this.parking,
      exclusiveArea: this.exclusiveArea == null ? null : parseFloat(this.exclusiveArea),
      floor: this.floor == null ? null : parseInt(this.floor),
      direction: this.direction,
      households: this.households == null ? null : parseInt(this.households),
      manage: this.manage,
      balcony: this.balcony,
      coverage: this.coverage,
      volume: this.volume,
      buildingArea: this.buildingArea == null ? null : parseFloat(this.buildingArea),
      landArea: this.landArea == null ? null : parseFloat(this.landArea)
    }
  }
}

export default RentPiece



