<template>
<div class="app-content-auto-height" v-if="manager.estateSellPieceService.sellPieceDetail">
  <vue-headful title="ブドウさん - 物件詳細"/>

  <p class="md-title"><md-icon>business</md-icon>{{manager.estateSellPieceService.sellPieceDetail.name}}</p>
  <p class="md-caption"><md-icon>room</md-icon>{{manager.estateSellPieceService.sellPieceDetail.address}}</p>

  <div class="md-layout md-gutter md-alignment-center-center">
    <div class="md-layout-item md-size-40 md-small-size-50 md-xsmall-size-100">
      <md-content class="viewer-contaner">
        <photoViewer :manager="manager" :photos="manager.estateSellPieceService.sellPieceDetail.photos" v-if="manager.estateSellPieceService.sellPieceDetail.photos && manager.estateSellPieceService.sellPieceDetail.photos.length > 0"/>
      </md-content>
      <md-content class="viewer-title">
        外観写真
      </md-content>
    </div>
    <div class="md-layout-item md-size-40 md-small-size-50 md-xsmall-size-100">
      <md-content class="viewer-contaner">
        <photoViewer :manager="manager" :photos="manager.estateSellPieceService.sellPieceDetail.floorPlans" v-if="manager.estateSellPieceService.sellPieceDetail.floorPlans && manager.estateSellPieceService.sellPieceDetail.floorPlans.length > 0"/>
      </md-content>
      <md-content class="viewer-title">
        間取り図
      </md-content>
    </div>
  </div>

  <div class="detail-container">
    <sellPieceDetailInfo :manager="manager" :piece="manager.estateSellPieceService.sellPieceDetail"/>
  </div>

  <md-content class="footer-title" v-if="manager.estateSellPieceService.sellPieceDetail.previews && manager.estateSellPieceService.sellPieceDetail.previews.length > 0">
    360度ビューでお部屋を体験
  </md-content>
  <div class="md-layout md-alignment-center-center preview-container" v-if="manager.estateSellPieceService.sellPieceDetail.previews && manager.estateSellPieceService.sellPieceDetail.previews.length > 0">
    <div class="md-layout-item md-size-20 md-medium-size-25 md-small-size-33 md-xsmall-size-50" v-for="(preview, i) in manager.estateSellPieceService.sellPieceDetail.previewThumbnails" :key="i">
      <img class="preview-photo" :src="preview" @click="previewPiece(i)">
    </div>
  </div>

  <md-content class="footer-title">
    アクセス
  </md-content>
  <md-content class="map-container">
    <gmap-map class="gmap" :center="center" :zoom="14">
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="true"
        :draggable="true"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>
  </md-content>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import photoViewer from '@/components/common/photoViewer'
import sellPieceDetailInfo from './sellPieceDetailInfo'
export default {
  props: ['manager'],
  components: {
    photoViewer,
    sellPieceDetailInfo
  },
  data: () => ({
    center: {lat: 35.416645, lng: 140.042037},
    markers: [{
      position: {lat: 35.416645, lng: 140.042037}
    }]
  }),
  methods: {
    previewPiece (index) {
      utils.openInNewTab('/sellPiecePreview/' + manager.estateSellPieceService.sellPieceDetail._id + '/' + index)
    }
  },
  beforeDestroy () {
    manager.estateSellPieceService.sellPieceDetail = null
  }
}
</script>

<style scoped lang="scss">
.md-title {
  height: 30px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  .md-icon {
    margin: 0 10px 0 0;
  }
}
.md-caption {
  height: 20px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  .md-icon {
    font-size: 18px !important;
    margin: 0 10px 0 0;
  }
  margin-bottom: 30px;
}
.viewer-contaner {
  width: 100%;
  height: 400px;
  border: 1px solid #e8e8e8;
}
.detail-container {
  width: 100%;
  margin: 40px 0px;
}
.viewer-title {
  width: 100%;
  height: 50px;
  background: #e8e8e8;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  color: #454545;
  margin-bottom: 20px;
}
.preview-container {
  border: 1px solid #e8e8e8;
}
.preview-photo {
  cursor: pointer;
}
.preview-photo:hover {
  opacity: 0.7;
}
.footer-title {
  width: 100%;
  height: 50px;
  background: #e8e8e8;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  color: #454545;
  margin-top: 20px;
}
.gmap {
  width: 100%;
  height: 400px
}
</style>



