<template>
<div class="app-content-fix-height" @click="showBody">
  <router-view :manager="manager"/>
  <navigator ref="navigator" :manager="manager"/>
</div>

</template>

<script>
import navigator from './navigator'
export default {
  props: ['manager'],
  components: {
    navigator
  },
  methods: {
    showBody () {
      this.$refs.navigator.isActive = false
    }
  }
}
</script>

<style scoped>

</style>



