<template>
<div class="chat-actions" v-if="manager.chatService.room">
  <div class="chat-text-input">
    <md-field>
      <label>チャット本文</label>
      <md-textarea ref="chattext" v-model="text" @keydown.enter="isSend = true" @keyup.enter="send" @keydown.ctrl="ctrl = true" @keyup.ctrl="ctrl = false"></md-textarea>
      <span class="md-helper-text">Ctrl + Enter で改行</span>
    </md-field>
  </div>
  <div class="chat-button">
    <div class="chat-tools">
      <md-button class="md-primary md-elevation-5">
        <div class="button-text">
          <md-icon>attachment</md-icon>
        </div>
      </md-button>
      <md-menu style="margin: 6px 8px;" :md-offset-x="-160" :md-offset-y="-260">
        <md-button class="md-primary md-elevation-5" md-menu-trigger>
          <md-icon >insert_emoticon</md-icon>
        </md-button>
        <md-menu-content class="emotion-container">
          <md-content md-theme="warm" class="md-primary emotion" v-for="(emotion, key) in this.manager.const.emotion" :key="key">
            <img :src="emotion" @click.stop="insertEmotion(key)">
          </md-content>
        </md-menu-content>
      </md-menu>
    </div>
    <div class="chat-send">
      <md-button class="md-primary md-raised" @click="clickSend">
        <div class="button-text">
          <md-icon>send</md-icon><p>送信</p>
        </div>
      </md-button>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager'],
  data: () => ({
    text: '',
    ctrl: false,
    isSend: false
  }),
  methods: {
    clickSend () {
      this.isSend = true
      this.send()
    },
    send () {
      if (this.ctrl) {
        this.isSend = false
        this.text = this.text + '\n'
        return
      }
      if (this.text) this.text = this.text.trim()
      if (!this.text || this.text === '') return
      if (!this.isSend) return
      let now = new Date()
      manager.socketService.emit('sendChat', {
        _id: '' + (manager.chatService.room.chats.length + 1),
        text: this.text,
        chatTime: now.valueOf(),
        user: 'rrrr'
      })
      this.text = ''
      this.isSend = false
    },
    insertEmotion (e) {
      let that = this
      let value = utils.insertAtCursor(this.$refs.chattext.$el, this.text, '[' + e + ']')
      this.text = value[0]
      this.$nextTick(() => {
        that.$refs.chattext.$el.selectionEnd = value[1]
      })
    }
  }
}
</script>

<style scoped lang="scss">
.chat-actions {
  width: 100%;
  height: 168px;
  padding: 10px;
  overflow-y: hidden;
  display: flex;
  justify-content: flex-start;
}
.chat-text-input {
  width: calc(100% - 104px);
}
textarea {
  resize: none !important;
}
.md-helper-text {
  width: 100%;
  text-align: right;
  padding-right: 5px;
}
.chat-button {
  width: 104px;
  height: 100%;
}
.chat-send {
  height: 48px;
}
.chat-tools {
  height: calc(100% - 48px);
}
.md-menu-content {
  width: 250px;
}
.emotion {
  width: 28px;
  height: 28px;
  float: left;
  margin: 1px;
  padding: 4px;
  border-radius: 3px;
  cursor: pointer;
}
.emotion:hover {
  opacity: .8;
}
</style>

<style lang="scss">
.emotion-container .md-list {
  margin: 0;
  padding: 5px;
  display: block;
  overflow: hidden;
}
</style>



