<template>
<md-content class="content-wrapper md-scrollbar" ref="contentWarper">
  <vue-headful title="ブドウさん - 賃貸物件管理"/>
  <div class="subtitle-warper">
    <span class="subTitle">賃貸物件管理</span>
    <md-button class="md-icon-button md-raised md-accent button-createnew" to="/my/rentPiece/new">
      <md-icon>add</md-icon>
    </md-button>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-100">
      <md-table>
        <pagination
          :md-page-size="manager.myRentService.pagesize"
          :md-total="manager.myRentService.count"
          :md-page="manager.myRentService.pageindex"
          md-label="Rows"
          md-separator="of"
          :md-page-options="[5, 10, 25, 50]"
          @goToPrevious="goToPrevious"
          @goToPage="goToPage"
          @goToNext="goToNext">
        </pagination>
      </md-table>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100" v-for="piece in manager.myRentService.rentPieces" :key="piece._id">
      <myRentPieceCard :manager="manager" :object="piece"/>
    </div>
    <div class="md-layout-item md-size-100">
      <md-table>
        <pagination
          :md-page-size="manager.myRentService.pagesize"
          :md-total="manager.myRentService.count"
          :md-page="manager.myRentService.pageindex"
          md-label="Rows"
          md-separator="of"
          :md-page-options="[5, 10, 25, 50]"
          @goToPrevious="goToPrevious"
          @goToPage="goToPage"
          @goToNext="goToNext">
        </pagination>
      </md-table>
    </div>
  </div>
</md-content>
</template>

<script>
import pagination from '@/components/common/pagination'
import myRentPieceCard from '@/components/shared/myRentPieceCard'
export default {
  props: ['manager'],
  components: {
    myRentPieceCard,
    pagination
  },
  methods: {
    goToPrevious: function () {
      let that = this
      let toPage = this.manager.myRentService.pageindex - 1
      this.manager.myRentService.pageindex = toPage
      this.manager.myRentService.findRentPiece({}, toPage).then(() => {
        that.$nextTick(() => {
          that.$refs.contentWarper.$el.scrollTop = 0
        })
      })
    },
    goToPage: function (e) {
      let that = this
      this.manager.myRentService.pageindex = e.page
      this.manager.myRentService.findRentPiece({}, e.page).then(() => {
        that.$nextTick(() => {
          that.$refs.contentWarper.$el.scrollTop = 0
        })
      })
    },
    goToNext: function () {
      let that = this
      let toPage = this.manager.myRentService.pageindex + 1
      this.manager.myRentService.pageindex = toPage
      this.manager.myRentService.findRentPiece({}, toPage).then(() => {
        that.$nextTick(() => {
          that.$refs.contentWarper.$el.scrollTop = 0
        })
      })
    }
  }
}
</script>

<style scoped>
.content-wrapper {
  height: 100%;
  overflow: auto;
  padding: 20px;
}
.subtitle-warper{
  position: relative;
  padding: 15px;
}
.subTitle{
  font-size: 30px;
  font-weight: 500;
}
.button-createnew{
  position: absolute;
  top: 0;
  right: 50px;
}
.md-layout-item{
  margin-bottom: 10px;
}
</style>



