import moment from 'moment'
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import Contact from './contact.js'

class SellPiece {
  constructor (data) {
    this._id = data._id
    // 公開済み
    this._isPublishing = data.isPublishing || 0

    // ----------------------------
    // 共通属性
    // ----------------------------

    // 業者
    this.contact = null
    if (data.contact) this.contact = new Contact(data.contact)
    // 写真
    this.photos = data.photos || []
    this.photoThumbnails = data.photoThumbnails || []
    // VRプレビュー
    this.previews = data.previews || []
    this.previewThumbnails = data.previewThumbnails || []
    // 間取り図
    this.floorPlans = data.floorPlans || []
    this.floorPlanThumbnails = data.floorPlanThumbnails || []

    // 物件名
    this.name = data.name || null
    // アドレス
    this.address = data.address || null
    // 建物種別（マンション・アパート・一戸建・その他）
    this._type = '1'
    if (data.type) this.type = data.type
    // 価格
    this._price = null
    if (data.price != null) this.price = data.price
    // 駅・線路・徒歩時間
    this.stations = []
    this.setStations(data.stations)
    // 階建て
    this._story = null
    if (data.story != null) this.story = data.story
    // 建物構造（鉄筋・鉄骨・木造・その他）
    this._structure = null
    if (data.structure) this.structure = data.structure
    // 築年月
    this._built = null
    if (data.built) this.built = data.built
    // 新築
    this.isNew = data.isNew || null
    // 取引態様
    this._aspect = null
    if (data.aspect) this.aspect = data.aspect
    // 間取り
    this._layout = null
    if (data.layout) this.layout = data.layout
    // 仲介手数料
    this._marginFee = null
    if (data.marginFee != null) this.marginFee = data.marginFee
    this._marginPercent = null
    if (data.marginPercent != null) this.marginPercent = data.marginPercent
    // 備考
    this.comment = data.comment || null
    // 現況
    this._available = null
    if (data.available) this.available = data.available
    // 入居可能日
    this._residentable = null
    if (data.residentable) this.residentable = data.residentable
    // 権利形態（所有権、借地権）
    this._rights = null
    if (data.rights) this.rights = data.rights
    // 掲載日
    this._cdate = null
    if (data.cdate) this.cdate = data.cdate
    // 更新日
    this._udate = null
    if (data.udate) this.udate = data.udate

    // ----------------------------
    // マンション・アパートの場合
    // ----------------------------

    // 専有面積
    this._exclusiveArea = null
    if (data.exclusiveArea != null) this.exclusiveArea = data.exclusiveArea
    // 階層
    this._floor = null
    if (data.floor != null) this.floor = data.floor
    // 方位
    this._direction = null
    if (data.direction) this.direction = data.direction
    // 総戸数
    this._households = null
    if (data.households != null) this.households = data.households
    // 管理費
    this._manage = null
    if (data.manage != null) this.manage = data.manage
    // 修繕費
    this._repair = null
    if (data.repair != null) this.repair = data.repair
    // バルコニー有無
    this.balcony = data.balcony || null
    // 駐車場有無
    this.parking = data.parking || null
    // ペット
    this.pet = data.pet || null
    // エアコン
    this.airConditioning = data.airConditioning || null

    // ----------------------------
    // 一戸建・その他の場合
    // ----------------------------

    // 建ぺい率
    this._coverage = null
    if (data.coverage != null) this.coverage = data.coverage
    // 容積率
    this._volume = null
    if (data.volume != null) this.volume = data.volume
    // 建物面積
    this._buildingArea = null
    if (data.buildingArea != null) this.buildingArea = data.buildingArea
    // 土地面積
    this._landArea = null
    if (data.landArea != null) this.landArea = data.landArea
  }

  get id () {
    return this._id
  }
  get isPublishing () {
    return !!this._isPublishing
  }
  set isPublishing (data) {
    this._isPublishing = data ? 1 : 0
  }
  get built () {
    return this._built ? this._built.valueOf() : null
  }
  get builtText () {
    let text = ''
    if (this._built) text += this._built.format('YYYY年MM月') + '/'
    text += this.age
    return text
  }
  get builtInputText () {
    return this._built ? this._built.format('YYYYMM') : ''
  }
  set built (data) {
    if (data) this._built = moment(data)
    else this._built = null
  }
  get residentable () {
    return this._residentable ? this._residentable.valueOf() : null
  }
  get residentableText () {
    return this._residentable ? this._residentable.format('YYYY年MM月DD日') : '-'
  }
  set residentable (data) {
    if (data) this._residentable = moment(data)
    else this._residentable = null
  }
  get parkingText () {
    return this.parking ? '有' : '無'
  }
  get balconyText () {
    return this.balcony ? '有' : '無'
  }
  get petText () {
    return this.pet ? '可' : '否'
  }
  get airConditioningText () {
    return this.airConditioning ? '有' : '無'
  }
  get price () {
    return this._price == null ? null : parseInt(this._price)
  }
  get priceText () {
    return this.price != null ? utils.formatHugeMoney(this.price) : '-'
  }
  set price (data) {
    if (data != null) this._price = parseInt(data)
    else this._price = null
  }
  get story () {
    return this._story == null ? null : parseInt(this._story)
  }
  get storyText () {
    return this.story != null ? utils.formatNumber(this.story) : '-'
  }
  set story (data) {
    if (data != null) this._story = parseInt(data)
    else this._story = null
  }
  get floor () {
    return this._floor == null ? null : parseInt(this._floor)
  }
  get floorText () {
    let text = '-'
    if (this.isApartment) {
      if (this.floor > 0) {
        text = '' + this.floor + '階目'
      }
      if (this.story > 0) {
        if (text !== '-') {
          text += ' / '
        } else {
          text = ''
        }
        text += this.story + '階建'
      }
    } else if (this.isDetached) {
      if (this.story > 0) {
        text = '' + this.story + '階建'
      }
    }
    return text
  }
  set floor (data) {
    if (data != null) this._floor = parseInt(data)
    else this._floor = null
  }
  get households () {
    return this._households == null ? null : parseInt(this._households)
  }
  get householdsText () {
    return this.households != null ? utils.formatNumber(this.households) : '-'
  }
  set households (data) {
    if (data != null) this._households = parseInt(data)
    else this._households = null
  }
  get manage () {
    return this._manage == null ? null : parseInt(this._manage)
  }
  get manageText () {
    return this.manage != null ? utils.formatHugeMoney(this.manage) : '-'
  }
  set manage (data) {
    if (data != null) this._manage = parseInt(data)
    else this._manage = null
  }
  get exclusiveArea () {
    return this._exclusiveArea == null ? null : parseFloat(this._exclusiveArea)
  }
  get exclusiveAreaText () {
    return this.exclusiveArea ? utils.formatNumber(this.exclusiveArea) + 'm²' : '-'
  }
  set exclusiveArea (data) {
    if (data != null) this._exclusiveArea = parseFloat(data)
    else this._exclusiveArea = null
  }
  get buildingArea () {
    return this._buildingArea == null ? null : parseFloat(this._buildingArea)
  }
  get buildingAreaText () {
    return this.buildingArea ? utils.formatNumber(this.buildingArea) + 'm²' : '-'
  }
  set buildingArea (data) {
    if (data != null) this._buildingArea = parseFloat(data)
    else this._buildingArea = null
  }
  get landArea () {
    return this._landArea == null ? null : parseFloat(this._landArea)
  }
  get landAreaText () {
    return this.landArea ? utils.formatNumber(this.landArea) + 'm²' : '-'
  }
  set landArea (data) {
    if (data != null) this._landArea = parseFloat(data)
    else this._landArea = null
  }
  get repair () {
    return this._repair == null ? null : parseInt(this._repair)
  }
  get repairText () {
    return this.repair != null ? utils.formatHugeMoney(this.repair) : '-'
  }
  set repair (data) {
    if (data != null) this._repair = parseInt(data)
    else this._repair = null
  }
  get marginFee () {
    return this._marginFee == null ? null : parseInt(this._marginFee)
  }
  get marginFeeText () {
    return this.marginFee != null ? utils.formatHugeMoney(this.marginFee) : '-'
  }
  set marginFee (data) {
    if (data != null) this._marginFee = parseInt(data)
    else this._marginFee = null
  }
  get marginPercent () {
    return this._marginPercent == null ? null : parseFloat(this._marginPercent)
  }
  get marginPercentText () {
    return this.marginPercent != null ? utils.formatPercentage(this.marginPercent) : '-'
  }
  set marginPercent (data) {
    if (data != null) this._marginPercent = parseFloat(data)
    else this._marginPercent = null
  }
  get coverage () {
    return this._coverage == null ? null : parseFloat(this._coverage)
  }
  get coverageText () {
    return this.coverage != null ? utils.formatPercentage(this.coverage) : '-'
  }
  set coverage (data) {
    if (data != null) this._coverage = parseFloat(data)
    else this._coverage = null
  }
  get volume () {
    return this._volume == null ? null : parseFloat(this._volume)
  }
  get volumeText () {
    return this.volume != null ? utils.formatPercentage(this.volume) : '-'
  }
  set volume (data) {
    if (data != null) this._volume = parseFloat(data)
    else this._volume = null
  }
  get cdate () {
    return this._cdate ? this._cdate.valueOf() : null
  }
  get cdateText () {
    return this._cdate != null ? this._cdate.format('YYYY年MM月DD日') : '-'
  }
  set cdate (data) {
    if (data) this._cdate = moment(data)
    else this._cdate = null
  }
  get udate () {
    return this._udate ? this._udate.valueOf() : null
  }
  get udateText () {
    return this._udate != null ? this._udate.format('YYYY年MM月DD日') : '-'
  }
  set udate (data) {
    if (data) this._udate = moment(data)
    else this._udate = null
  }

  get aspect () {
    return this._aspect
  }
  get aspectText () {
    return this.aspect != null ? manager.const.aspect[this.aspect] : '-'
  }
  set aspect (data) {
    this._aspect = data
  }
  get available () {
    return this._available
  }
  get availableText () {
    return this.available != null ? manager.const.available[this.available] : '-'
  }
  set available (data) {
    this._available = data
  }
  get direction () {
    return this._direction
  }
  get directionText () {
    return this.direction != null ? manager.const.direction[this.direction] : '-'
  }
  set direction (data) {
    this._direction = data
  }
  get layout () {
    return this._layout
  }
  get layoutText () {
    return this.layout != null ? manager.const.layout[this.layout] : '-'
  }
  set layout (data) {
    this._layout = data
  }
  get rights () {
    return this._rights
  }
  get rightsText () {
    return this.rights != null ? manager.const.rights[this.rights] : '-'
  }
  set rights (data) {
    this._rights = data
  }
  get structure () {
    return this._structure
  }
  get structureText () {
    return this.structure != null ? manager.const.structure[this.structure] : '-'
  }
  set structure (data) {
    this._structure = data
  }
  get type () {
    return this._type
  }
  get typeText () {
    return this.type != null ? manager.const.type[this.type] : '-'
  }
  set type (data) {
    this._type = data
  }

  // 築年数
  get age () {
    if (this.isNew) {
      return '新築'
    } else if (this._built) {
      if (moment().diff(this._built, 'month') <= 0) {
        return '新築'
      } else {
        const years = moment().diff(this._built, 'year')
        const months = moment().diff(this._built, 'month') - (years * 12)
        return '' + years + '年' + months + '月前'
      }
    } else {
      return '-'
    }
  }
  // 手数料
  get marginText () {
    let text = ''
    if (this.marginPercent) {
      text += '取引金額の' + this.marginPercentText
    }
    if (this.marginFee) {
      if (text !== '') {
        text += ' + '
      }
      text += this.marginFeeText
    }
    if (text === '') text = '-'
    return text
  }
  // 交通情報
  setStations (stations) {
    if (stations) {
      this.stations.splice(0, this.stations.length)
      stations.forEach(one => {
        if (one.station && one.walking) {
          this.stations.push({
            station: one.station,
            walking: parseInt(one.walking)
          })
        }
      })
    }
  }

  get isApartment () {
    return (this.type === '1' || this.type === '2' || this.type === '4')
  }
  get isDetached () {
    return (this.type === '3' || this.type === '4')
  }

  toJSON () {
    return {
      _id: this.id,
      name: this.name,
      isPublishing: this.isPublishing,
      contactID: this.contact ? this.contact._id : null,
      photos: this.photos,
      photoThumbnails: this.photoThumbnails,
      floorPlans: this.floorPlans,
      floorPlanThumbnails: this.floorPlanThumbnails,
      stations: this.stations,
      address: this.address,
      price: this.price,
      aspect: this.aspect,
      layout: this.layout,
      story: this.story,
      floor: this.floor,
      exclusiveArea: this.exclusiveArea,
      buildingArea: this.buildingArea,
      landArea: this.landArea,
      type: this.type,
      structure: this.structure,
      built: this.built,
      isNew: this.isNew,
      previews: this.previews,
      previewThumbnails: this.previewThumbnails,
      direction: this.direction,
      households: this.households,
      parking: this.parking,
      manage: this.manage,
      repair: this.repair,
      marginFee: this.marginFee,
      marginPercent: this.marginPercent,
      available: this.available,
      residentable: this.residentable,
      rights: this.rights,
      balcony: this.balcony,
      pet: this.pet,
      airConditioning: this.airConditioning,
      coverage: this.coverage,
      volume: this.volume,
      comment: this.comment
    }
  }
}

export default SellPiece



