import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import SellPiece from '@/store/models/sellPiece.js'

class MySellPieceService {
  constructor () {
    this._sellPieces = []
    this._editSellPiece = null
    this._count = 0
    this.pagesize = 10
    this.pageindex = 1
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get sellPieces () {
    return this._sellPieces
  }
  get editSellPiece () {
    return this._editSellPiece
  }
  set editSellPiece (piece) {
    this._editSellPiece = new SellPiece(piece)
  }

  init () {
    this._sellPieces.splice(0, this._sellPieces.length)
    this._count = 0
    this.pageindex = 1
  }

  preloadMySell = (to, from, next) => {
    console.log('preloadMySell')
    this.init()
    this.findSellPiece({contact: manager.user._id}, 1).then(
      () => {
        next()
      }
    )
  }

  async findSellPiece (params, page) {
    let that = this
    if (page && page >= 1) page--
    await utils.restGet('/private/findSellPieces', {filter: params, page: page}).then(response => {
      let datas = response.datas
      that.count = response.count
      that._sellPieces.splice(0, that._sellPieces.length)
      if (datas) {
        datas.forEach(one => {
          that._sellPieces.push(new SellPiece(one))
        })
      }
    })
  }

  preloadMySellPiece = (to, from, next) => {
    let that = this
    if (to.params.id === 'new') {
      that.editSellPiece = new SellPiece({})
      next()
    } else {
      utils.restGet('/private/findSellPieces', {'_id': to.params.id}).then(
        response => {
          if (response) {
            that.editSellPiece = new SellPiece(response.datas[0])
            next()
          }
        }
      )
    }
  }

  insertSellPiece (piece, next) {
    utils.restPut('/private/insertSellPiece', piece).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  async publishSellPiece (piece) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/publishSellPiece', {_id: piece._id, isPublishing: true}).then(savedPiece => {
      if (!savedPiece) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findSellPiece({}, 1)
  }

  async unPublishSellPiece (piece) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/unPublishSellPiece', {_id: piece._id, isPublishing: false}).then(savedPiece => {
      if (!savedPiece) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findSellPiece({}, 1)
  }

  updateSellPiece (piece, next) {
    utils.restPost('/private/updateSellPiece', piece).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  deleteSellPiece (piece, next) {
    let that = this
    utils.restPost('/private/deleteSellPiece', {_id: piece._id}).then(savedPiece => {
      if (savedPiece) {
        that.init()
        that.findSellPiece({}, 1)
        next()
      }
    })
  }
}

export default new MySellPieceService()



