<template>
<div>
  <div class="md-layout md-gutter md-alignment-left">
    <p class="md-title title-padding">物件情報</p>
    <div class="md-layout-item md-size-100 md-layout">
      <div class="md-layout-item md-size-25 md-small-size-55">
        <md-radio v-model="manager.myRentService.editRentPiece.type" value="1">{{manager.const.type[1]}}</md-radio>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-45">
        <md-radio v-model="manager.myRentService.editRentPiece.type" value="2">{{manager.const.type[2]}}</md-radio>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-55">
        <md-radio v-model="manager.myRentService.editRentPiece.type" value="3">{{manager.const.type[3]}}</md-radio>
      </div>
      <div class="md-layout-item md-size-25 md-small-size-45">
        <md-radio v-model="manager.myRentService.editRentPiece.type" value="4">{{manager.const.type[4]}}</md-radio>
      </div>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-33 md-small-size-100">
      <md-field md-clearable :class="{'md-invalid': !isNameValid}">
        <label for="name">物件名</label>
        <md-input v-model="manager.myRentService.editRentPiece.name" required></md-input>
        <span class="md-error" v-if="!manager.myRentService.editRentPiece.name">入力してください</span>
        <span class="md-error" v-else>20文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-66 md-small-size-100">
      <md-field md-clearable :class="{'md-invalid': !isAddressValid}">
        <label for="address">アドレス</label>
        <md-input v-model="manager.myRentService.editRentPiece.address" required></md-input>
        <span class="md-error" v-if="!manager.myRentService.editRentPiece.address">入力してください</span>
        <span class="md-error" v-else>60文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isPriceValid}">
        <label>価格</label>
        <md-input v-model="manager.myRentService.editRentPiece.price" type="number" required></md-input>
        <span class="md-helper-text">{{manager.myRentService.editRentPiece.priceText}}</span>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100">
      <md-field>
        <label>現況</label>
        <md-select v-model="manager.myRentService.editRentPiece.available">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(available, key) in manager.const.available" :key="key">{{available}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100">
      <md-field>
        <label>間取り</label>
        <md-select v-model="manager.myRentService.editRentPiece.layout">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(layout, key) in manager.const.layout" :key="key">{{layout}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100" v-if="manager.myRentService.editRentPiece.isApartment">
      <md-field :class="{'md-invalid': !isFloorValid}">
        <label>所在階</label>
        <md-input v-model="manager.myRentService.editRentPiece.floor" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isStoryValid}">
        <label>階層</label>
        <md-input v-model="manager.myRentService.editRentPiece.story" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100" v-if="manager.myRentService.editRentPiece.isApartment">
      <md-field :class="{'md-invalid': !isExclusiveAreaValid}">
        <label>専有面積</label>
        <md-input v-model="manager.myRentService.editRentPiece.exclusiveArea" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100" v-if="manager.myRentService.editRentPiece.isDetached">
      <md-field :class="{'md-invalid': !isBuildingAreaValid}">
        <label>建築面積</label>
        <md-input v-model="manager.myRentService.editRentPiece.buildingArea" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100" v-if="manager.myRentService.editRentPiece.isDetached">
      <md-field :class="{'md-invalid': !isLandAreaValid}">
        <label>土地面積</label>
        <md-input v-model="manager.myRentService.editRentPiece.landArea" type="number"></md-input>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100">
      <md-field>
        <label>構造</label>
        <md-select v-model="manager.myRentService.editRentPiece.structure">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(structure, key) in manager.const.structure" :key="key">{{structure}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100 input-group">
      <div class="input-left">
        <md-field :class="{'md-invalid': !isBuiltValid}">
          <label>築年月</label>
          <md-input v-model="built" type="text" maxlength="6"></md-input>
          <span class="md-helper-text">例：201802</span>
          <span class="md-error">年月を入力してください</span>
        </md-field>
      </div>
      <div class="input-right">
        <md-checkbox v-model="manager.myRentService.editRentPiece.isNew" class="md-primary">新築</md-checkbox>
      </div>
    </div>
  </div>
  <div class="md-layout">
    <p class="md-title">交通情報</p>
    <div class="md-layout-item md-size-100 md-small-size-100 md-layout trafic">
      <div class="md-layout-item md-size-100 md-layout md-gutter station md-elevation-1" v-for="(station, index) in stations" :key="index">
        <div class="md-layout-item md-size-40">
          <bdsMdChips v-model="station.station" :md-format="filterStation" :mdSelections="selections" :isSingleSelect="true" mdPlaceholder="最寄り駅"></bdsMdChips>
        </div>
        <div class="md-layout-item md-size-40">
          <md-field :class="{'md-invalid': !isWalkingValid(station.walking)}">
            <label>徒歩時間</label>
            <md-input v-model="station.walking" type="number"></md-input>
            <span class="md-error">正数を入力してください</span>
          </md-field>
        </div>
        <div class="md-layout-item md-size-20 remove-button">
          <md-button class="md-icon-button md-raised" @click.stop="removeStation(index)">
            <md-icon>close</md-icon>
          </md-button>
        </div>
      </div>
      <md-button class="md-icon-button md-raised" @click.stop="addStation">
        <md-icon>add</md-icon>
      </md-button>
    </div>
  </div>
  <p class="md-title">業者情報</p>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <md-field>
        <label>取引態様</label>
        <md-select v-model="manager.myRentService.editRentPiece.aspect">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(aspect, key) in manager.const.aspect" :key="key">{{aspect}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100 md-layout md-gutter">
      <div class="md-layout-item md-size-50">
        <md-field :class="{'md-invalid': !isMarginPercentValid}">
          <label>手数料(金額比)</label>
          <md-input v-model="manager.myRentService.editRentPiece.marginPercent" type="number"></md-input>
          <span class="md-helper-text">取引金額の{{manager.myRentService.editRentPiece.marginPercentText}}</span>
          <span class="md-error">正数を入力してください</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-50">
        <md-field :class="{'md-invalid': !isMarginFeeValid}">
          <label>手数料(固定)</label>
          <md-input v-model="manager.myRentService.editRentPiece.marginFee" type="number"></md-input>
          <span class="md-helper-text">{{manager.myRentService.editRentPiece.marginFeeText}}</span>
          <span class="md-error">正数を入力してください</span>
        </md-field>
      </div>
    </div>
    <div class="md-layout-item md-size-100">
      <md-field :class="{'md-invalid': !isCommentValid}">
        <label>コメント</label>
        <md-input v-model="manager.myRentService.editRentPiece.comment"></md-input>
        <span class="md-error" v-if="!manager.myRentService.editRentPiece.comment">入力してください</span>
        <span class="md-error" v-else>60文字以内</span>
      </md-field>
    </div>
  </div>
</div>
</template>

<script>
import moment from 'moment'
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import bdsMdChips from '@/components/common/vue-material/bdsMdChips'
export default {
  props: ['manager'],
  components: {
    bdsMdChips
  },
  data: () => ({
    selections: [],
    stations: [],
    built: null
  }),
  created () {
    let that = this
    that.selections.splice(0, that.selections.length)
    for (let line in manager.const.station) {
      manager.const.station[line].forEach(station => {
        that.selections.push({value: station.sn, name: line + '-' + station.sn})
      })
    }
    manager.myRentService.editRentPiece.stations.forEach(one => {
      that.stations.push({station: [one.station], walking: one.walking})
    })
    this.built = manager.myRentService.editRentPiece.builtInputText
    this.addStation()
  },
  computed: {
    isNameValid () {
      if (manager.myRentService.editRentPiece.name === '') return false
      if (manager.myRentService.editRentPiece.name && manager.myRentService.editRentPiece.name.length > 20) return false
      return true
    },
    isAddressValid () {
      if (manager.myRentService.editRentPiece.address === '') return false
      if (manager.myRentService.editRentPiece.address && manager.myRentService.editRentPiece.address.length > 60) return false
      return true
    },
    isPriceValid () {
      if (manager.myRentService.editRentPiece.price === null) return true
      if (manager.myRentService.editRentPiece.price <= 0) return false
      return true
    },
    isStoryValid () {
      if (manager.myRentService.editRentPiece.story === null) return true
      if (manager.myRentService.editRentPiece.story <= 0) return false
      return true
    },
    isFloorValid () {
      if (!manager.myRentService.editRentPiece.floor) return true
      if (manager.myRentService.editRentPiece.floor <= 0) return false
      return true
    },
    isExclusiveAreaValid () {
      if (!manager.myRentService.editRentPiece.exclusiveArea) return true
      if (manager.myRentService.editRentPiece.exclusiveArea <= 0) return false
      return true
    },
    isBuildingAreaValid () {
      if (!manager.myRentService.editRentPiece.buildingArea) return true
      if (manager.myRentService.editRentPiece.buildingArea <= 0) return false
      return true
    },
    isLandAreaValid () {
      if (!manager.myRentService.editRentPiece.landArea) return true
      if (manager.myRentService.editRentPiece.landArea <= 0) return false
      return true
    },
    isBuiltValid () {
      if (!this.built) return true
      if (this.built.length < 6) return false
      if (!moment(this.built, 'YYYYMM').isValid()) return false
      return true
    },
    isMarginPercentValid () {
      if (!manager.myRentService.editRentPiece.marginPercent) return true
      if (manager.myRentService.editRentPiece.marginPercent <= 0) return false
      return true
    },
    isMarginFeeValid () {
      if (!manager.myRentService.editRentPiece.marginFee) return true
      if (manager.myRentService.editRentPiece.marginFee <= 0) return false
      return true
    },
    isCommentValid () {
      if (!manager.myRentService.editRentPiece.comment) return true
      if (manager.myRentService.editRentPiece.comment && manager.myRentService.editRentPiece.comment.length > 60) return false
      return true
    }
  },
  methods: {
    isWalkingValid (walking) {
      if (!walking) return true
      if (parseInt(walking) <= 0) return false
      return true
    },
    addStation () {
      this.stations.push({station: [], walking: null})
    },
    removeStation (index) {
      this.stations.splice(index, 1)
    },
    filterStation (input) {
      if (!input) return
      let selection = null
      this.selections.some(one => {
        const isAdded = this.selections.some(station => {
          if (station === one.value) return true
        })
        if (!isAdded && one.name.indexOf(input) >= 0) {
          selection = one.value
          return true
        }
      })
      return selection
    },
    validate () {
      if (!manager.myRentService.editRentPiece.name) {
        return '物件名は必須入力項目です。'
      }
      if (!manager.myRentService.editRentPiece.address) {
        return 'アドレスは必須入力項目です。'
      }
      if (!manager.myRentService.editRentPiece.price) {
        return '価格は必須入力項目です。'
      }
      if (!this.isNameValid || !this.isAddressValid || !this.isPriceValid || !this.isStoryValid || !this.isFloorValid || !this.isExclusiveAreaValid || !this.isBuildingAreaValid || !this.isLandAreaValid || !this.isBuiltValid || !this.isMarginPercentValid || !this.isMarginFeeValid || !this.isCommentValid) {
        return '入力不正です。'
      }
      let stations = []
      for (let i = this.stations.length - 1; i >= 0; i--) {
        let one = this.stations[i]
        if (!one.station || one.station.length <= 0 || !one.walking) {
          this.stations.splice(i, 1)
        } else {
          stations.push({
            station: one.station[0],
            walking: parseInt(one.walking)
          })
        }
      }
      manager.myRentService.editRentPiece.setStations(stations)
      manager.myRentService.editRentPiece.built = this.built + '01'
      return null
    }
  }
}
</script>

<style lang="scss" scoped>
.title-padding {
  padding-left: 20px;
}
.input-group {
  display: flex;
}
.input-left {
  width: calc(100% - 100px);
}
.input-right {
  width: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.trafic {
  padding: 0 20px 20px 20px;
}
.station {
  padding: 5px;
  margin-bottom: 10px;
}
.remove-button {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>



