<template>
<div class="app-content-auto-height" @click="showBody">
  <vue-headful title="ブドウさん - 物件取引"/>

  <router-view :manager="manager"/>
  <div class="estate-guide" v-if="route === 'estate'">
    <div>
      <div>
        <md-button class="md-raised md-accent button-guide" to="/estate/sellPiece">売出物件を探す</md-button>
        <md-button class="md-raised md-accent button-guide" to="/estate/buyRequest">買取を探す</md-button>
      </div>
      <div>
        <md-button class="md-raised md-accent button-guide" to="/estate/rentPiece">貸付物件を探す</md-button>
        <md-button class="md-raised md-accent button-guide" to="/estate/borrowRequest">借入要望を探す</md-button>
      </div>
    </div>
  </div>
  <navigator ref="navigator" :manager="manager"/>
</div>
</template>

<script>
import navigator from './navigator'
export default {
  props: ['manager'],
  components: {
    navigator
  },
  computed: {
    route () {
      return this.$route.name
    }
  },
  methods: {
    showBody () {
      this.$refs.navigator.isActive = false
    }
  }
}
</script>

<style scoped>
.estate-guide {
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button-guide {
  width: 170px;
  height: 64px;
  margin: 16px;
}
</style>



