<template>
  <div class="app-content-fix-height scene-container">
    <vue-headful title="ブドウさん - 内覧"/>
    <a-scene embedded v-if="manager.estateSellPieceService.sellPieceDetail">
      <a-assets>
        <img id="photo-view" :src="view" crossorigin="anonymous">
        <!-- <img id="photo-info" :src="info"> -->
      </a-assets>
      <a-sky id="image-360" radius="10" src="#photo-view"></a-sky>
      <!-- <a-entity class="link" geometry="primitive: plane; height: 1; width: 1" material="shader: flat; src: #photo-info" position="5 5 -5"> -->
      <a-entity class="link" geometry="primitive: plane; height: 1; width: 1" position="0 0 0">
      </a-entity>
      <a-camera>
        <a-cursor id="cursor">
          <a-animation begin="click" easing="ease-in" attribute="scale" fill="backwards" from="0.1 0.1 0.1" to="1 1 1" dur="150"></a-animation>
          <a-animation begin="cursor-fusing" easing="ease-in" attribute="scale" from="1 1 1" to="0.1 0.1 0.1" dur="1500"></a-animation>
        </a-cursor>
      </a-camera>
    </a-scene>
  </div>
</template>

<script>
import manager from '@/store/manager.js'
// import utils from '@/tool/utils.js'
export default {
  props: ['manager'],
  data: () => ({
    view: null
    // info: '../../../static/miraimon.png'
  }),
  mounted () {
    let index = parseInt(this.$route.params.index)
    this.view = manager.estateSellPieceService.sellPieceDetail.previews[index]
  },
  beforeDestroy () {
    manager.estateSellPieceService.sellPieceDetail = null
  }
}
</script>

<style scoped>
.scene-container {
  display: flex;
  justify-content: center;
  /* padding: 15px; */
}
/* a-scene {
  width: 998px;
  height: 700px;
} */
.app-content-fix-height {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>



